<template>
    
    <div class="town" >

        <div class="town-locations-grid" v-if="!shopModeSelected">
            <h2>TOWN</h2>
            <h3>Buy or sell body parts on the Black Market, or request intelligence services of highly suspicious individuals in the Den of Outlaws.</h3>
            <ul class="locations-list">
                <li @click="blackMarketOpen=true" class="btn">Black Market</li>
                <li>Den of Outlaws</li>
                <li @click="backToMain" class="btn">Return</li>
            </ul>
        </div>

        <!-- black market -->
        

        <div class="black-market" v-if="blackMarketOpen">
            <div class="black-market-selection-grid">
                <div class="shop-mode-selection" v-if="!shopModeSelected">
                    <h2>SHOP</h2>
                    <ul class="shop-selection-btns" >
                        <li @click="selectedShopMode('buy')" class="btn">Buy items</li>
                        <li @click="selectedShopMode('sell')" class="btn">Sell items</li>
                    </ul>
                </div>
                
                
                <div class="black-market-shop-grid" v-if="shopModeSelected && selectedBuy" >
                    <shop-component :itemsList="itemsList" :goldAmount="goldAmount" @closeShop="update" @addItemToInventory="addBoughtItem($event)"></shop-component>
                </div>

                <div class="black-market-sell-grid" v-if="shopModeSelected && selectedSell">
                    <h2>SELL ITEMS</h2>
                    <h3 class="gold">Gold: {{playerGold}}</h3>
                    <div class="sell-items-grid">
                        <h3 v-if="noItems">No items in the inventory.</h3>
                        <!-- player inventory display -->
                    <div class="items-container" v-if="!noItems">
            <div class="crafting-materials" v-if="!noCrafts">
                <h3>Body parts:</h3>
                <div class="heads-container" v-if="!noHeads">
                    <h4>Heads:</h4>
        <item v-for="(item, index) in playerItemsHeads" :key='"heads"+index' :item="item" @click.native="selectItem(item)" :sellValue="sellValueFormula(item)"></item>
                </div>
                <div class="arms-container" v-if="!noArms">
                    <h4>Arms:</h4>
        <item v-for="(item, index) in playerItemsArms" :key='"arms"+index' :item="item" @click.native="selectItem(item)" :sellValue="sellValueFormula(item)"></item>
                </div>
                <div class="torsos-container" v-if="!noTorsos">
                    <h4>Torsos:</h4>
        <item v-for="(item, index) in playerItemsTorsos" :key='"torsos"+index' :item="item" @click.native="selectItem(item)" :sellValue="sellValueFormula(item)"></item>
                </div>
                <div class="legs-container" v-if="!noLegs">
                     <h4>Legs:</h4>
        <item v-for="(item, index) in playerItemsLegs" :key='"legs"+index' :item="item" @click.native="selectItem(item)" :sellValue="sellValueFormula(item)"></item>
                </div>
            
                <div class="brains-container" v-if="!noBrains">
                    <h4>Brains:</h4>
        <item v-for="(item, index) in playerItemsBrains" :key='"brains"+index' :item="item" @click.native="selectItem(item)" :sellValue="sellValueFormula(item)"></item>
                </div>
                <div class="hearts-container" v-if="!noHearts">
                    <h4>Hearts:</h4>
        <item v-for="(item, index) in playerItemsHearts" :key='"hearts"+index' :item="item" @click.native="selectItem(item)" :sellValue="sellValueFormula(item)"></item>
                </div>

                
            
            
            </div>
            <div class="Necromancy books" v-if="!noNecBooks">
                <h3>Books:</h3>
        <item v-for="(item, index) in playerItemsNecroBooks" :key='"necbooks"+index' :item="item" @click.native="selectItem(item)" :sellValue="sellValueFormula(item)"></item>
                
            </div>
            <div class="recipes" v-if="!noNecroRecipes"><h3>Necro Recipes:</h3></div>
            <div class="necro-recipes-container" v-if="!noNecroRecipes">
                    <h4>Necromancy spells:</h4>
        <item v-for="(item, index) in playerItemsNecroRecipes" :key='"necroRecipes"+index' :item="item" @click.native="selectItem(item)" :sellValue="sellValueFormula(item)"></item>
                </div>

            <div class="recipes" v-if="!noDemoRecipes"><h3>Demo Recipes:</h3></div>
            <div class="demo-recipes-container" v-if="!noDemoRecipes">
                    <h4>Demonology spells:</h4>
        <item v-for="(item, index) in playerItemsDemoRecipes" :key='"deniRecipes"+index' :item="item" @click.native="selectItem(item)" :sellValue="sellValueFormula(item)"></item>
                </div>

                <div class="recipes" v-if="!noDemoBooks"><h3>Demo Books:</h3></div>
            <div class="demo-recipes-container" v-if="!noDemoBooks">
                    <h4>Demonology books:</h4>
        <item v-for="(item, index) in playerItemsDemoBooks" :key='"demoBooks"+index' :item="item" @click.native="selectItem(item)" :sellValue="sellValueFormula(item)"></item>
                </div>
            
    
        </div>
                </div>

            <div class="selected-item">
                <h3>Selected item:</h3>
                <item :item="selectedItem" v-if="itemSelected"></item>
                <div class="no-item-selected-text" v-if="!itemSelected">
                    Select an item to sell
                </div>
                <div class="gold-cost-display">
                    Sell value: {{currentItemSellValue}}
                </div>
            </div>

                    <div class="shop-sell-btns">
                        <div class="shop-sell-btn btn" @click="sell">
                            SELL
                        </div>
                        <div class="shop-return-btn btn" @click="closeSellShop">
                            RETURN
                        </div>
                    </div>

                </div>

            </div>
            
        </div>

    </div>

</template>

<script>

import shopComponent from '@/components/shopComponent.vue';
import { shop } from '@/stateManagement/itemsCollection.js';
import { items } from '@/stateManagement/itemsCollection.js';
import {gameStore} from '@/stateManagement/gameStore.js';
import item from '@/components/itemComponent.vue';

export default {
    components: {
        shopComponent,
        item
    },
    data() {
        return {
            shopModeSelected: false,
            selectedBuy: false,
            selectedSell: false,
            blackMarketOpen: false,
            itemsList: [],
            itemSelected: false,
            selectedItem: {},

            playerItemsHeads: gameStore.playerCharacter.inventory.craftingParts.heads,
            playerItemsArms: gameStore.playerCharacter.inventory.craftingParts.arms,
            playerItemsTorsos: gameStore.playerCharacter.inventory.craftingParts.torsos,
            playerItemsLegs: gameStore.playerCharacter.inventory.craftingParts.legs,
            playerItemsHearts: gameStore.playerCharacter.inventory.craftingParts.hearts,
            playerItemsBrains: gameStore.playerCharacter.inventory.craftingParts.brains,
            playerItemsNecroBooks: gameStore.playerCharacter.inventory.necroBooks,
            playerItemsNecroRecipes: gameStore.playerCharacter.inventory.necroRecipes,
            playerItemsDemoRecipes: gameStore.playerCharacter.inventory.demoRecipes,
            playerItemsDemoBooks: gameStore.playerCharacter.inventory.demoBooks,
        }
    },
    methods: {
        backToMain() {
            this.$emit('closeShop');
        },
        selectedShopMode(mode) {
            if (mode === "sell") {
                this.selectedSell = true;
            } else if (mode === 'buy') {
                this.selectedBuy = true;
            }
            this.shopModeSelected = true;
            this.createItemsList();
            
            
        },
        createItemsList() {
            //roll if items list should be randomised
            //helper function
            function randomIntNumberFromInterval(min, max) {return Math.floor(Math.random() * (max - min + 1) + min);}

            let checkResetRoll = randomIntNumberFromInterval(1, 10);

            if (JSON.stringify(this.itemsList) === '[]') {
                //roll for items in itemsList
                this.itemsList = shop.firstShopItems;
            } else if (checkResetRoll > 5 ) {
                this.itemsList = [];
                this.itemsList.push(items.heads[0]);
            }
        },
        update($event) {
            gameStore.playerCharacter.gold = $event;
            this.shopModeSelected=false;
            this.selectedBuy=false;
            this.selectedSell=false;
            this.blackMarketOpen=false;
        },
        addBoughtItem($event) {
            
            if ($event.function === "craft") {
                gameStore.playerCharacter.inventory.craftingParts[$event.type].push($event);
            } else if ($event.function === "necroBook") {
                
                gameStore.playerCharacter.inventory.necroBooks.push($event);
                
            } else if ($event.function === "necroRecipes") {
                gameStore.playerCharacter.inventory.necroRecipes.push($event);
            }
            
        },
        selectItem(item) {
            this.itemSelected=true;
            this.selectedItem = item;
        },
        sell() {
            
            gameStore.playerCharacter.gold += this.currentItemSellValue;
            if ( this.selectedItem.function === 'craft' && this.selectedItem.type === 'legs') {
                let index = gameStore.playerCharacter.inventory.craftingParts.legs.indexOf(this.selectedItem)
                gameStore.playerCharacter.inventory.craftingParts.legs.splice(index, 1);
            } else if (this.selectedItem.function === 'craft' && this.selectedItem.type === 'arms') {
                let index = gameStore.playerCharacter.inventory.craftingParts.arms.indexOf(this.selectedItem);
                gameStore.playerCharacter.inventory.craftingParts.arms.splice(index, 1);
            } else if (this.selectedItem.function === 'craft' && this.selectedItem.type === 'heads') {
                let index = gameStore.playerCharacter.inventory.craftingParts.heads.indexOf(this.selectedItem);                
                gameStore.playerCharacter.inventory.craftingParts.heads.splice(index, 1);
            } else if (this.selectedItem.function === 'craft' && this.selectedItem.type === 'brains') {
                let index = gameStore.playerCharacter.inventory.craftingParts.brains.indexOf(this.selectedItem);                
                gameStore.playerCharacter.inventory.craftingParts.brains.splice(index, 1);
            } else if (this.selectedItem.function === 'craft' && this.selectedItem.type === 'hearts') {
                let index = gameStore.playerCharacter.inventory.craftingParts.hearts.indexOf(this.selectedItem);
                gameStore.playerCharacter.inventory.craftingParts.hearts.splice(index, 1);
            } else if (this.selectedItem.function === 'craft' && this.selectedItem.type === 'torsos') {
                let index = gameStore.playerCharacter.inventory.craftingParts.torsos.indexOf(this.selectedItem);
                gameStore.playerCharacter.inventory.craftingParts.torsos.splice(index, 1);                
            } else if (this.selectedItem.function === 'necroBook' && this.selectedItem.type === 'necroBooks' ) {
                let index = gameStore.playerCharacter.inventory.necroBooks.indexOf(this.selectedItem);
                gameStore.playerCharacter.inventory.necroBooks.splice(index, 1);                
            } else if (this.selectedItem.function === 'necroRecipes' && this.selectedItem.type === 'necroRecipes') {
                let index = gameStore.playerCharacter.inventory.necroRecipes.indexOf(this.selectedItem);
                gameStore.playerCharacter.inventory.necroRecipes.splice(index, 1);                              
            } else if (this.selectedItem.function === 'demoRecipes' && this.selectedItem.type === 'demoRecipes') {
                let index = gameStore.playerCharacter.inventory.demoRecipes.indexOf(this.selectedItem);
                gameStore.playerCharacter.inventory.demoRecipes.splice(index, 1);
            } else if (this.selectedItem.function === 'demoBook' && this.selectedItem.type === 'demoBooks') {
                let index = gameStore.playerCharacter.inventory.demoBooks.indexOf(this.selectedItem);
                gameStore.playerCharacter.inventory.demoBooks.splice(index, 1);
            } 

            this.selectedItem = {};
            this.itemSelected = false;

        },
        closeSellShop() {
            this.shopModeSelected = false;
            this.selectedBuy = false;
            this.selectedSell = false;
            this.blackMarketOpen = false;
        },
        sellValueFormula(item) {
            return Math.floor(item.cost/3);
        }
    },
    computed: {
        goldAmount() {
            return gameStore.playerCharacter.gold;
        },
        noItems() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts) === JSON.stringify({arms: [], legs: [], heads: [], torsos: [], brains: [], hearts: []}) && JSON.stringify(gameStore.playerCharacter.inventory.necroBooks) === "[]"&& JSON.stringify(gameStore.playerCharacter.inventory.necroRecipes) === "[]"  ) {
                return true;
            } else {
                return false;
            }
        },
        noNecBooks() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.necroBooks) === "[]") {
                return true
            } else {
                return false;
            }
        },
        noDemoBooks() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.demoBooks) === "[]") {
                return true
            } else {
                return false;
            }
        },
        noRecipes() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.recipes) === "[]") {
                return true;
            } else {
                return false;
            }
        },
        noCrafts() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts) === JSON.stringify({arms: [], legs: [], heads: [], torsos: [], brains: [], hearts: []})) {
                return true;
            } else {
                return false;
            }
        },
        noHearts() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts.hearts) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noBrains() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts.brains) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noLegs() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts.legs) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noArms() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts.arms) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noTorsos() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts.torsos) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noHeads() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts.heads) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noNecroRecipes() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.necroRecipes) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noDemoRecipes() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.demoRecipes) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        currentItemSellValue() {
            if (JSON.stringify(this.selectedItem) !== "{}") {
                return Math.floor(this.selectedItem.cost/3);
            } else {
                return 0;
            }
           
        },
        playerGold() {
            return gameStore.playerCharacter.gold;
        }
    }
}
</script>

<style scoped>
.town {
    background: #1c1c1c;
    color: #fff;
    height: 100vh;
    width: 100%;
    position: absolute;
    display: grid;    
    align-content: center;
    z-index: 2;
}

.town-locations-grid {
    display: grid;
    justify-content: center;
    align-content: center;
    text-align: center;
}

.town-locations-grid h2, h3 {
    margin-bottom: 1rem;
    padding: 0 1rem;
}

.locations-list {
    justify-self: center;
}

.locations-list li {
    text-align: center;
    margin: .4rem;
    padding: .5rem 3rem;
    background-color: #262424;
    border: #000 1px solid;
    font-size: 1.5rem;
}

.shop-mode-selection {
    background: #1c1c1c;
    color: #fff;
    height: 100vh;
    width: 100%;
    position: absolute;
    display: grid;    
    align-content: center;
    z-index: 2;
    top:0;
}

.shop-mode-selection h2 {
    text-align: center;
}

.shop-selection-btns {
    justify-self: center;
}

.shop-selection-btns li {
    text-align: center;
    margin: .4rem;
    padding: .5rem 3rem;
    background-color: #262424;
    border: #000 1px solid;
    font-size: 1.5rem;
    
}
/* display section */

.crafting-materials h3, h4{
    text-align: center;
}

.black-market-shop-grid {
    position: relative;
    display: grid;
    justify-content: center;
    align-items: center;
}

.black-market-sell-grid {
        height: 100vh;
    display: grid;
}

.black-market-sell-grid h2 {
    text-align: center;
}
.black-market-sell-grid .gold {
    border-bottom: 1px solid #000;
    padding-bottom: .5rem;
}

.sell-items-grid {
    overflow-y: auto;
}

.items-container {
    display: grid;
    justify-items: center;

}
 /* selected item section */
.selected-item {
    border-top: 1px solid #000;
    padding: 0 .5rem ;
}
.selected-item h3 {
    margin-top: .5rem;
}

.shop-sell-btns {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    align-content: center;
}



.btn {
    text-align: center;
    margin: .4rem;
    padding: .5rem .3rem;
    background-color: #262424;
    border: #000 1px solid;
    font-size: 1.5rem;
}

</style>
