<template>

    <div class="inventory">
        <h2>INVENTORY</h2>
        <h4 v-if="noItems">No items in the inventory</h4>
        <div class="items-container" v-if="!noItems">
            <div class="crafting-materials" v-if="!noCrafts">
                <h3>Body parts:</h3>
                <div class="heads-container" v-if="!noHeads">
                    <h4>Heads:</h4>
        <item v-for="(item, index) in playerItemsHeads" :key='"heads"+index' :item="item"></item>
                <div class="btn" v-if="expandBtnHeads" @click="showAll('Heads')">EXPAND</div>
                </div>
                <div class="arms-container" v-if="!noArms">
                    <h4>Arms:</h4>
        <item v-for="(item, index) in playerItemsArms" :key='"arms"+index' :item="item"></item>
                <div class="btn" v-if="expandBtnArms" @click="showAll('Arms')">EXPAND</div>
        
                </div>
                <div class="torsos-container" v-if="!noTorsos">
                    <h4>Torsos:</h4>
        <item v-for="(item, index) in playerItemsTorsos" :key='"torsos"+index' :item="item"></item>
                <div class="btn" v-if="expandBtnTorsos" @click="showAll('Torsos')">EXPAND</div>
        
                </div>
                <div class="legs-container" v-if="!noLegs">
                     <h4>Legs:</h4>
        <item v-for="(item, index) in playerItemsLegs" :key='"legs"+index' :item="item"></item>
                <div class="btn" v-if="expandBtnLegs" @click="showAll('Legs')">EXPAND</div>
        
                </div>
            
                <div class="brains-container" v-if="!noBrains">
                    <h4>Brains:</h4>
        <item v-for="(item, index) in playerItemsBrains" :key='"brains"+index' :item="item"></item>
                <div class="btn" v-if="expandBtnBrains" @click="showAll('Brains')">EXPAND</div>
        
                </div>
                <div class="hearts-container" v-if="!noHearts">
                    <h4>Hearts:</h4>
        <item v-for="(item, index) in playerItemsHearts" :key='"hearts"+index' :item="item"></item>
                <div class="btn" v-if="expandBtnHearts" @click="showAll('Hearts')">EXPAND</div>
        
                </div>

                
            
            
            </div>
            <div class="Necromancy books" v-if="!noNecBooks">
                <h3>Necromancy: </h3>
                <h3>Necromancy Books:</h3>
        <item v-for="(item, index) in playerItemsNecroBooks" :key='"necbooks"+index' :item="item"></item>
                <div class="btn" v-if="expandBtnNecroBooks" @click="showAll('NecroBooks')">EXPAND</div>
        
                
            </div>
            <div class="recipes" v-if="!noNecroRecipes"><h3>Necromancy spells:</h3></div>
            <div class="necro-recipes-container" v-if="!noNecroRecipes">
                    
                    <item v-for="(item, index) in playerItemsNecroRecipes" :key='"necroRecipes"+index' :item="item"></item>
                <div class="btn" v-if="expandBtnNecroRecipes" @click="showAll('NecroRecipes')">EXPAND</div>
        
                </div>
                <div class="demo-recipes" v-if="!noDemoRecipes"> <h3>Demonology:</h3> </div>
                <div class="demo-recipes-container" v-if="!noDemoRecipes">
                    <h4>Demonology spells:</h4>
                    <item v-for="(item, index) in playerItemsDemoRecipes" :key='"demoRecipes"+index' :item="item"></item>
                    <div class="btn" v-if="expandBtnDemoRecipes" @click="showAll('DemoRecipes')">EXPAND</div>
                    
                    
                </div>
                <div class="demo-books-container" v-if="!noDemoBooks">
                    <h4>Demonology books:</h4>
                    <item v-for="(item, index) in playerItemsDemoBooks" :key='"demoBooks"+index' :item="item"></item>
                    <div class="btn" v-if="expandBtnDemoBooks" @click="showAll('DemoBooks')">EXPAND</div>
                    
                    
                </div>
                
        </div>
        
        <div class="btns-grid">
            
            <div class="close-inventory btn" @click="returnToMain"> RETURN </div>
        </div>
        
    </div>

</template>

<script>

import {gameStore} from '@/stateManagement/gameStore.js';
import item from '@/components/itemComponent.vue';


export default {
    components: {
        item
    },
    data() {
        return {
            expandHeads: false,
            expandArms: false,
            expandTorsos: false,
            expandLegs: false,
            expandHearts: false,
            expandBrains: false,
            expandNecroBooks: false,
            expandNecroRecipes: false,
            expandDemoRecipes: false,
            expandDemoBooks: false,
        }
    },
    methods: {
        returnToMain() {
            this.$emit('closeInventory');
        },
        showAll(items) {
            let target = 'expand'+items;
            this[target] = true;
        }
    },
    computed: {
        noItems() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts) === JSON.stringify({arms: [], legs: [], heads: [], torsos: [], brains: [], hearts: []}) && JSON.stringify(gameStore.playerCharacter.inventory.necroBooks) === "[]" && JSON.stringify(gameStore.playerCharacter.inventory.necroRecipes) === "[]" && JSON.stringify(gameStore.playerCharacter.inventory.demoRecipes) === '[]'&& JSON.stringify(gameStore.playerCharacter.inventory.demoBooks) === '[]') {
                return true;
            } else {
                return false;
            }
        },
        noNecBooks() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.necroBooks) === "[]") {
                return true
            } else {
                return false;
            }
        },
        noDemoBooks() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.demoBooks) === "[]") {
                return true
            } else {
                return false;
            }
        },
        noRecipes() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.recipes) === "[]") {
                return true;
            } else {
                return false;
            }
        },
        noCrafts() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts) === JSON.stringify({arms: [], legs: [], heads: [], torsos: [], brains: [], hearts: []})) {
                return true;
            } else {
                return false;
            }
        },
        noHearts() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts.hearts) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noBrains() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts.brains) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noLegs() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts.legs) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noArms() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts.arms) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noTorsos() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts.torsos) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noHeads() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.craftingParts.heads) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noNecroRecipes() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.necroRecipes) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        noDemoRecipes() {
            if (JSON.stringify(gameStore.playerCharacter.inventory.demoRecipes) ==='[]' ) {
                return true;
            } else {
                return false;
            }
        },
        playerItemsHeads() {
            if (!this.expandHeads) {
                return gameStore.playerCharacter.inventory.craftingParts.heads.slice(0, 16);
            } else {
                return gameStore.playerCharacter.inventory.craftingParts.heads;
                }
        },
        expandBtnHeads() {
            if (this.playerItemsHeads.length < gameStore.playerCharacter.inventory.craftingParts.heads.length) {
                return true;
            } else {
                return false;
            }
        },
        playerItemsArms() {
            if (!this.expandArms) {
                return gameStore.playerCharacter.inventory.craftingParts.arms.slice(0, 16);
                
            } else {
                return gameStore.playerCharacter.inventory.craftingParts.arms;
                }
        },
        expandBtnArms() {
            if (this.playerItemsArms.length < gameStore.playerCharacter.inventory.craftingParts.arms.length) {
                return true;
            } else {
                return false;
            }
        },
        playerItemsTorsos() {
            if (!this.expandTorsos) {
                return gameStore.playerCharacter.inventory.craftingParts.torsos.slice(0, 16);
                
            } else {
                return gameStore.playerCharacter.inventory.craftingParts.torsos;
                }
        },
        expandBtnTorsos() {
            if (this.playerItemsTorsos.length < gameStore.playerCharacter.inventory.craftingParts.torsos.length) {
                return true;
            } else {
                return false;
            }
        },
        playerItemsLegs() {
            if (!this.expandLegs) {
                return gameStore.playerCharacter.inventory.craftingParts.legs.slice(0, 16);
                
            } else {
                return gameStore.playerCharacter.inventory.craftingParts.legs;
                }
        },
        expandBtnLegs() {
            if (this.playerItemsLegs.legs < gameStore.playerCharacter.inventory.craftingParts.heads.legs) {
                return true;
            } else {
                return false;
            }
        },
        playerItemsBrains() {
            if (!this.expandBrains) {
                return gameStore.playerCharacter.inventory.craftingParts.brains.slice(0, 16);
                
            } else {
                return gameStore.playerCharacter.inventory.craftingParts.brains;
                }
        },
        expandBtnBrains() {
            if (this.playerItemsBrains.length < gameStore.playerCharacter.inventory.craftingParts.heads.brains) {
                return true;
            } else {
                return false;
            }
        },
        playerItemsHearts() {
            if (!this.expandHearts) {
                return gameStore.playerCharacter.inventory.craftingParts.hearts.slice(0, 16);
                
            } else {
                return gameStore.playerCharacter.inventory.craftingParts.hearts;
                }
        },
        expandBtnHearts() {
            if (this.playerItemsHearts.length < gameStore.playerCharacter.inventory.craftingParts.hearts.length) {
                return true;
            } else {
                return false;
            }
        },
        playerItemsNecroBooks() {
            if (!this.expandNecroBooks) {
                return gameStore.playerCharacter.inventory.necroBooks.slice(0, 16);
                
            } else {
                return gameStore.playerCharacter.inventory.necroBooks;
                }
        },
         playerItemsDemoBooks() {
            if (!this.expandDemoBooks) {
                return gameStore.playerCharacter.inventory.demoBooks.slice(0, 16);
                
            } else {
                return gameStore.playerCharacter.inventory.demoBooks;
                }
        },
        expandBtnNecroBooks() {
            if (this.playerItemsNecroBooks.length < gameStore.playerCharacter.inventory.necroBooks.length) {
                return true;
            } else {
                return false;
            }
        },
        expandBtnDemoBooks() {
            if (this.playerItemsDemoBooks.length < gameStore.playerCharacter.inventory.demoBooks.length) {
                return true;
            } else {
                return false;
            }
        },
         playerItemsNecroRecipes() {
            if (!this.expandNecroRecipes) {
                return gameStore.playerCharacter.inventory.necroRecipes.slice(0, 16);
                
            } else {
                return gameStore.playerCharacter.inventory.necroRecipes;
                }
        },
        playerItemsDemoRecipes() {
            if (!this.expandDemoRecipes) {
                return gameStore.playerCharacter.inventory.demoRecipes.slice(0, 16);
            } else {
                return gameStore.playerCharacter.inventory.demoRecipes;
            }
        },
        expandBtnDemoRecipes() {
            if (this.playerItemsDemoRecipes.length < gameStore.playerCharacter.inventory.demoRecipes.length) {
                return true;
            } else {
                return false;
            }
        },
        expandBtnNecroRecipes() {
            if (this.playerItemsNecroRecipes.length < gameStore.playerCharacter.inventory.necroRecipes.length) {
                return true;
            } else {
                return false;
            }
        },
    }
}
</script>

<style scoped>

.inventory {
    background: #1c1c1c;
    color: #fff;
    height: 100vh;
    width: 100%;
    position: absolute;
    display: grid;
    justify-content: center;
    align-content: center;
    z-index: 2;
}

.inventory h2 {
    padding: 1rem;
    text-align: center;
}

h3 {
    font-size: 1.3rem;
    text-align: center;
}

.inventory h4 {
    font-size: 1.2rem;
    text-align: center;

}

.items-container {
    display: grid;
    overflow-y: auto;

}

.btns-grid {
    display: grid;
    
}

.btn {
    padding: .5rem 1rem;
    margin: .5rem;
    background-color: #262424;
    border: #000 1px solid;
    text-align: center;
}
</style>
