import Vue from 'vue'

export const gameStore = new Vue({
    data: {
        gameState: {
            isNewGame: false,
            characterCreated: false,
            turnCounter: 1,
            madeCreatures: 0,
            enemyUnits: [],
            
        },
        playerCharacter: {},
        eventLog: [],
    }
})

export const unitsOfOrder = new Vue ({
    data: {

    },
    methods: {
        UnitOfOrder(name, hp, maxHp, dmg, imgUrl, initiative, owner, absorption, regeneration, value, position) {
            this.name= name;
            this.hp = hp;
            this.maxHp = maxHp;
            this.dmg = dmg;
            this.imgUrl = imgUrl;
            this.initiative = initiative;
            this.owner = owner;
            this.absorption = absorption;
            this.regeneration = regeneration;
            this.value = value;
            this.position = position;
            },
        createPeasant() {
            let peasant = new this.UnitOfOrder("Peasant", 4, 4, 1, "peasant.svg", 1, "enemy", 0, 0, 1, 1);
            return peasant;
            },
        createBlacksmith() {
            let blacksmith = new this.UnitOfOrder("Blacksmith", 10, 10, 3, "blacksmith.svg", 3, "enemy", 0, 0, 3, 1);
            return blacksmith;
        },
        createPikeman() {
            let pikeman = new this.UnitOfOrder("Pikeman", 20, 20, 4, 'pikeman.svg', 6, 'enemy', 3, 0, 10, 1);
            return pikeman;
        },
        createExecutioner() {
            let executioner = new this.UnitOfOrder("Executioner", 36, 36, 8, 'executioner.svg', 4, 'enemy', 1, 0, 20, 1);
            return executioner;
        },
        createAssassin() {
            let assassin = new this.UnitOfOrder("Assassin", 27, 27, 21, 'assassin.svg', 23, 'enemy', 0, 0, 40, 1);
            assassin.poisonDuration = 3;
            assassin.poisonDmg = 2;
            return assassin;
        },
        createKnight() {
            let knight = new this.UnitOfOrder("Knight", 60, 60, 10, "knight.svg", 8, "enemy", 10, 0, 60, 1);
            return knight;
        }
        //name, hp, maxHp, dmg, imgUrl, initiative, owner, absorption, regeneration, value, position, 
    }
    
})