<template>
    <div class="graveyard">
        <h2>GRAVEYARD</h2>
        <h3>Desacrate this place to gather body parts for your experiments.</h3>
        
        <transition name="itemsGained" mode="out-in">
        <div class="graveyard-intro-img" v-if="!itemDisplay" key="graveyard">
            <img src="../assets/icons/grave-intro-img.svg" alt="">
        </div>

        <div class="item-drop-container" v-if="itemDisplay" key="items">
            <p>You desacrate the graves and unearth these items:</p>
            <div class="items-gained-grid">
                <item class="item"  v-for="(item, index) in itemsTemporary.arms" :key="'arms'+index" :item="item"></item>
                <item class="item"  v-for="(item, index) in itemsTemporary.heads" :key="'heads'+index" :item="item"></item>
                <item class="item"  v-for="(item, index) in itemsTemporary.torsos" :key="'torsos'+index" :item="item"></item>
                <item class="item"  v-for="(item, index) in itemsTemporary.legs" :key="'legs'+index" :item="item"></item>
                <item class="item"  v-for="(item, index) in itemsTemporary.brains" :key="'brains'+index" :item="item"></item>
                <item class="item"  v-for="(item, index) in itemsTemporary.hearts" :key="'hearts'+index" :item="item"></item>
                <item class="item"  v-for="(item, index) in itemsTemporary.necroBooks" :key="'necbooks'+index" :item="item"></item>
                <item class="item"  v-for="(item, index) in itemsTemporary.demoBooks" :key="'demobooks'+index" :item="item"></item>                
                <item class="item"  v-for="(item, index) in itemsTemporary.necroRecipes" :key="'necrecipes'+index" :item="item"></item>
                <item class="item"  v-for="(item, index) in itemsTemporary.demoRecipes" :key="'demorecipes'+index" :item="item"></item>
                
            </div>

            <div class="gold-gained">
                <div class="gold-display">
                    <span class="gold-found"></span> <span>You also managed to find a small amount of gold among the remains: {{goldFound}}</span>
                </div>
            </div>

        </div>
        </transition>

        <ul class="graveyard-actions-ul">
            <li class="dig-action btn" @click="digForCorpses">DIG FOR BODY PARTS <span class="action-points-counter">({{actionPoints}})</span></li>
            <li class="return-action btn" @click="returnToMain">RETURN</li>
        </ul>

        <!-- info display modal -->
        <transition name="show-modal">
        <div class="info-modal" v-if="displayInfo">
            <p>{{this.message}}</p> 
        </div>
            </transition>

    </div>
</template>

<script>
import {gameStore} from '@/stateManagement/gameStore.js';
import {items} from '@/stateManagement/itemsCollection.js';
import item from '@/components/itemComponent.vue';
import {setTimeout} from 'timers';

export default {
    props: ['actionPoints'],
    components: {
        item
    },
    data() {
        return {
            itemDisplay: false,
            itemsGained: items.heads,
            itemsTemporary: {arms: [], legs: [], heads: [], torsos: [], brains: [], hearts: [], necroBooks: [], necroRecipes: [], demoRecipes: [], demoBooks: []},
            displayInfo: false,
            
            goldFound: 0,
        }
    },
    methods: {
        returnToMain() {
            gameStore.playerCharacter.inventory.craftingParts.heads.push(...this.itemsTemporary.heads);
            gameStore.playerCharacter.inventory.craftingParts.arms.push(...this.itemsTemporary.arms);
            gameStore.playerCharacter.inventory.craftingParts.torsos.push(...this.itemsTemporary.torsos);
            gameStore.playerCharacter.inventory.craftingParts.legs.push(...this.itemsTemporary.legs);
            gameStore.playerCharacter.inventory.craftingParts.brains.push(...this.itemsTemporary.brains);
            gameStore.playerCharacter.inventory.craftingParts.hearts.push(...this.itemsTemporary.hearts);
            gameStore.playerCharacter.inventory.necroBooks.push(...this.itemsTemporary.necroBooks);
            gameStore.playerCharacter.inventory.demoBooks.push(...this.itemsTemporary.demoBooks);            
            gameStore.playerCharacter.inventory.necroRecipes.push(...this.itemsTemporary.necroRecipes);
            gameStore.playerCharacter.inventory.demoRecipes.push(...this.itemsTemporary.demoRecipes);
            
            
            this.$emit('closeGraveyard');
            
            gameStore.playerCharacter.gold += this.goldFound;
        },
        digForCorpses() {
            if (this.actionPoints > 0) {
                
                this.magicFindFormula();
                this.itemDisplay = true;
                this.$emit('corpsesDugForTheTurn');
                
            this.$emit('deductActionPoints');
            this.displayInfoFn('Items added to inventory.');         
            } else {
                this.displayInfoFn('Not enough action points to do that!');
                
                return;
            }
            
            
        },
        magicFindFormula () {
        //helper functions
        function randomIntNumberFromInterval(min, max) {return Math.floor(Math.random() * (max - min + 1) + min);}
        let rollTo100Fn = function(first, second) {
            let rollForItems = randomIntNumberFromInterval(1, 100);
            if ((rollForItems + gameStore.playerCharacter.magicFind()) > second) {
                return 3;
            } else if ( (rollForItems + gameStore.playerCharacter.magicFind()) > first) {                                
                return 2;
            } else {      
                return 1;
            }
        }
        
        //determine gold amount
        this.goldFound += randomIntNumberFromInterval(1,5);

        //determine how many items player gets
        let rolledItems = rollTo100Fn(40, 85);
        

        // determine the type of the item

        let determineType = function() {
            // ,'necroBook', 'demoBook', 'craft', 'recipe'
            let result = randomIntNumberFromInterval(1, 100);
            let itemType = '';
            if (result > 96) {
                itemType = 'necroBook';
            } else if (result > 91) {
                itemType = 'demoBook';
            } else if (result > 80) {
                itemType = 'recipe';
            } else {
                itemType = 'craft';
            }
            //determine which craft (if craft), or which recipe; it mutates itemType further 
            
            if (itemType === 'craft') {
                let craftTypes = [ 'heads', 'arms', 'torsos', 'legs', 'hearts', 'brains'];
                return craftTypes[randomIntNumberFromInterval(0, craftTypes.length-1)];
                
            } else if (itemType === 'recipe') {
                let craftTypes = [ 'necroRecipes', 'demoRecipes'];
                return craftTypes[randomIntNumberFromInterval(0, craftTypes.length-1)];
            } else {
                return itemType;
             }
            

        }
        
        
        
        
        //generate determined amount of items
        let self = this;
        function generateItems(item) {
            //determine the quality of the item 
            let determineQuality = randomIntNumberFromInterval(0, 100) + gameStore.playerCharacter.magicFind();
            let result = items[item].filter( el => el.value < determineQuality);
                let selectItem = randomIntNumberFromInterval(0, result.length-1);
                self.itemsTemporary[item].push(result[selectItem]);
        }
        

        for (let i = 0; i < rolledItems; i++) {
            let item = determineType();
            
            if (item === 'necroBook') {
                generateItems('necroBooks');
            } else if ( item === 'demoBook' ) {
                this.itemsTemporary.demoBooks.push(items.demoBooks[0])
            } else if ( item === 'heads' ) {
                generateItems('heads');
            } else if (item === 'arms') {
                generateItems('arms');
            } else if ( item === 'torsos' ) {
                generateItems('torsos');
            } else if (item === 'legs') {
                generateItems('legs');
            } else if (item === 'hearts') {
                generateItems('hearts');
            } else if (item === 'brains') {
                generateItems('brains');
            } else if (item === "necroRecipes") {
                generateItems('necroRecipes')
            } else if (item === "demoRecipes") {
                this.itemsTemporary.demoRecipes.push(items.demoRecipes[0]);
                
            }
            
        }
        
        
        // this.itemsTemporary.necroRecipes.push(items.necroRecipes[2]);
        // this.itemsTemporary.necroBooks.push(items.necroBooks[1]);
        
        
        //  this.itemsTemporary.demoRecipes.push(items.demoRecipes[0]);
        
        // this.itemsTemporary.arms.push(items.arms[3]);
        // this.itemsTemporary.heads.push(items.heads[4]);        
        // this.itemsTemporary.torsos.push(items.torsos[2]);
        // this.itemsTemporary.legs.push(items.legs[2]);
        // this.itemsTemporary.hearts.push(items.hearts[0]);
        // this.itemsTemporary.brains.push(items.brains[0]);
            
        },
        displayInfoFn(msg) {
            this.displayInfo = true;
            this.message = msg;
            setTimeout ( () => {
                this.displayInfo = false;
            }, 1100)
        }
    },
    computed: {
        
        
    }
}
</script>

<style scoped>
.graveyard {
    background: #1c1c1c;
    color: #fff;
    height: 100vh;
    width: 100%;
    position: absolute;
    display: grid;
    justify-content: center;
    align-content: center;
    z-index: 2;
    }

.graveyard h2, h3 {
    margin: 1rem 1rem 0 1rem;
    text-align: center;
}
.graveyard h3 {
    margin-bottom: 1rem;
}

.graveyard-intro-img {
    margin: 1rem;
}

/* info modal and its animation */
.info-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 1.3rem;
    padding: 2rem;
    background: rgba(0, 0, 0, .8);
    border-radius: 10px;
    width: 80%;
    text-align: center;
    transition: all .7s ease-in-out;
    display: grid;
    
        
    }

.show-modal-enter {
    opacity: 0;
}
.show-modal-to {
    opacity: 1;
}
.show-modal-leave-to {
    opacity: 0;
}

   
    /*end of info modal */

    .graveyard-actions-ul {
        text-align: center;
    }

    .graveyard-actions-ul li {
        padding: .4rem;
        margin: .4rem;
        background-color: #262424;
        border: #000 1px solid;
    }

    .item-drop-container {
        overflow-y: auto;
        
    }
    @media(min-width: 360px) {
        .item-drop-container {
            min-height: 430px
        }
    }
    .items-gained-grid {
        display: grid;
        
    }

    /* item styles */

    

    /* transition styles */
    
    .itemsGained-enter {
        transform: translateY(-100%);
        opacity: 0;
    }

    .itemsGained-enter-active {
        transition: all .3s ease-in;
    }

</style>
