<template>
    <div class="battle-screen">

        <!-- event log interchangeable with an active unit card display -->
        <div class="log-and-unit-card-container">
            <!-- event log -->
            <div class="event-log" v-show="!displayCard">
                <!-- <div class="event-log-title">Battle log: </div> -->
                <ul class="event-log-info-list scroll">
                    <li v-for="(entry, index) in updateBattleEventLog" :key="index" >{{entry}}</li>
                </ul>
            </div>
            <!-- unit card  -->
            <div class="active-unit-card" v-if="displayCard">
                <img :src="require('../assets/icons/' + activeUnit.imgUrl)" alt="" v-if="activeUnit.imgUrl">
                <div class="stat-block-card">
                    <div class="card-regular-stats">
                        <p v-if="activeUnit.name">{{activeUnit.name}}</p>
                <p v-if="activeUnit.hp">Hit Points: {{activeUnit.hp}}</p>
                <p v-if="activeUnit.dmg">Damage: {{activeUnit.dmg}}</p>
                <p v-if="activeUnit.initiative">Initiative: {{activeUnit.initiative}}</p>
                    </div>
                

                <div class="special-abilities">
                            <h4>Special abilities:</h4>
                            <ul class="special-abilities-list-sheet">
                                <li v-if="activeUnit.regeneration > 0">Regeneration: {{activeUnit.regeneration}}</li>
                                <li v-if="activeUnit.absorption > 0">Absorbtion: {{activeUnit.absorption}}</li>
                            </ul>
                            <h4>Status effects:</h4>
                            <ul class="special-abilities-list-sheet">
                                <li v-if="activeUnit.poisonTicks > 0">Poisoned!</li>
                                
                            </ul>
                        </div>
                </div>
                
                
                
            </div>
        </div>
        

            <!-- battle display -->

            <div class="battle-wrapper">
                <div class="battle-display-grid">

                    
                    
                    <div class="enemy-grid-wrapper">
                        <div class="enemy-units-grid">
                            
                            <div class="unit enemy-unit-1 enemy-unit" @click="attackFunction(enemyUnit1)" v-if="enemyUnit1">
                                <img :src="require('../assets/icons/' + enemyUnit1.imgUrl)" alt="" >
                                <div class="hp-display  " >{{enemyUnit1.hp}}</div>
                                <div class="enemy-unit-1-hit-anim"></div>
                            </div>
                            <div class="unit-placeholder " v-if="!enemyUnit1" >
                                <img src="../assets/icons/emptyfield.svg" alt="">
                                <div class="placeholder-enemy-1"></div>
                                
                            </div>

                            <div class="unit enemy-unit-2 enemy-unit" @click="attackFunction(enemyUnit2)" v-if="enemyUnit2">
                                <img :src="require('../assets/icons/' + enemyUnit2.imgUrl)" alt="">
                                <div class="hp-display  ">{{enemyUnit2.hp}}</div>
                                <div class="enemy-unit-2-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!enemyUnit2">
                                <div class="placeholder-enemy-2"></div>
                                
                            </div>
                            
                            <div class="unit enemy-unit-3 enemy-unit" @click="attackFunction(enemyUnit3)" v-if="enemyUnit3">
                                <img :src="require('../assets/icons/' + enemyUnit3.imgUrl)" alt="">
                                <div class="hp-display  ">{{enemyUnit3.hp}}</div>
                                <div class="enemy-unit-3-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!enemyUnit3">
                                <div class="placeholder-enemy-3"></div>
                                
                            </div>
                            

                             <div class="unit enemy-unit-4 enemy-unit" @click="attackFunction(enemyUnit4)" v-if="enemyUnit4">
                                <img :src="require('../assets/icons/' + enemyUnit4.imgUrl)" alt="">
                                <div class="hp-display  ">{{enemyUnit4.hp}}</div>
                                <div class="enemy-unit-4-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!enemyUnit4">
                                <div class="placeholder-enemy-4"></div>
                                
                            </div>
                            

                             <div class="unit enemy-unit-5 enemy-unit" @click="attackFunction(enemyUnit5)" v-if="enemyUnit5">
                                <img :src="require('../assets/icons/' + enemyUnit5.imgUrl)" alt="">
                                <div class="hp-display  ">{{enemyUnit5.hp}}</div>
                                <div class="enemy-unit-5-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!enemyUnit5">
                                <div class="placeholder-enemy-5"></div>
                                
                            </div>
                            

                             <div class="unit enemy-unit-6 enemy-unit" @click="attackFunction(enemyUnit6)" v-if="enemyUnit6">
                                <img :src="require('../assets/icons/' + enemyUnit6.imgUrl)" alt="">
                                <div class="hp-display  ">{{enemyUnit6.hp}}</div>
                                <div class="enemy-unit-6-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!enemyUnit6">
                                <div class="placeholder-enemy-6"></div>
                                
                            </div>
                            

                             <div class="unit enemy-unit-7 enemy-unit" @click="attackFunction(enemyUnit7)" v-if="enemyUnit7">
                                <img :src="require('../assets/icons/' + enemyUnit7.imgUrl)" alt="">
                                <div class="hp-display  ">{{enemyUnit7.hp}}</div>
                                <div class="enemy-unit-7-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!enemyUnit7">
                                <div class="placeholder-enemy-7"></div>
                                
                            </div>
                            

                             <div class="unit enemy-unit-8 enemy-unit" @click="attackFunction(enemyUnit8)" v-if="enemyUnit8">
                                <img :src="require('../assets/icons/' + enemyUnit8.imgUrl)" alt="">
                                <div class="hp-display  ">{{enemyUnit8.hp}}</div>
                                <div class="enemy-unit-8-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!enemyUnit8">
                                <div class="placeholder-enemy-8"></div>
                            </div>
                            
                        </div>                    
                    </div>

                  <div class="division"></div>

                    <div class="player-grid-wrapper">
                        
                        <div class="player-units-grid">

                            <div class="unit player-unit-1 player-unit" v-if="playerUnit1">
                                <img :src="require('../assets/icons/' + playerUnit1.imgUrl)" alt="">
                                <div class="hp-display  ">{{playerUnit1.hp}}</div>
                                <div class="player-unit-1-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!playerUnit1">
                                <div class="placeholder-player-1"></div>
                                <img src="../assets/icons/emptyfield.svg" alt="">
                                
                            </div>
                            

                            <div class="unit player-unit-2 player-unit" v-if="playerUnit2">
                                <img :src="require('../assets/icons/' + playerUnit2.imgUrl)" alt="">
                                <div class="hp-display  ">{{playerUnit2.hp}}</div>
                                <div class="player-unit-2-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!playerUnit2">
                                <div class="placeholder-player-2"></div>
                                
                            </div>
                            
                            <div class="unit player-unit-3 player-unit" v-if="playerUnit3">
                                <img :src="require('../assets/icons/' + playerUnit3.imgUrl)" alt="">
                                <div class="hp-display  ">{{playerUnit3.hp}}</div>
                                <div class="player-unit-3-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!playerUnit3">
                                <div class="placeholder-player-3"></div>
                            </div>
                            

                            <div class="unit player-unit-4 player-unit" v-if="playerUnit4">
                                <img :src="require('../assets/icons/' + playerUnit4.imgUrl)" alt="">
                                <div class="hp-display  ">{{playerUnit4.hp}}</div>
                                <div class="player-unit-4-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!playerUnit4">
                                <div class="placeholder-player-4"></div>
                            </div>
                            

                            <div class="unit player-unit-5 player-unit" v-if="playerUnit5">
                                <img :src="require('../assets/icons/' + playerUnit5.imgUrl)" alt="">
                                <div class="hp-display  ">{{playerUnit5.hp}}</div>
                                <div class="player-unit-5-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!playerUnit5">
                                <div class="placeholder-player-5"></div>
                                </div>
                            

                            <div class="unit player-unit-6 player-unit" v-if="playerUnit6">
                                <img :src="require('../assets/icons/' + playerUnit6.imgUrl)" alt="">
                                <div class="hp-display  ">{{playerUnit6.hp}}</div>
                                <div class="player-unit-6-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder" v-if="!playerUnit6">
                                <div class="placeholder-player-6"></div>
                                
                            </div>
                            

                            <div class="unit player-unit-7 player-unit" v-if="playerUnit7">
                                <img :src="require('../assets/icons/' + playerUnit7.imgUrl)" alt="">
                                <div class="hp-display  ">{{playerUnit7.hp}}</div>
                                <div class="player-unit-7-hit-anim">
                                </div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!playerUnit7">
                                <div class="placeholder-player-7"></div>
                                
                            </div>
                            

                            <div class="unit player-unit-8 player-unit" v-if="playerUnit8">
                                <img :src="require('../assets/icons/' + playerUnit8.imgUrl)" alt="">
                                <div class="hp-display  ">{{playerUnit8.hp}}</div>
                                <div class="player-unit-8-hit-anim"></div>
                                
                            </div>
                            <div class="unit-placeholder " v-if="!playerUnit8">
                                <div class="placeholder-player-8"></div>
                                
                            </div>
                            

                        </div>
                    </div>

                    
                    
                </div>
            </div>

            <!-- end of battle display -->

            <div class="buttons-wrapper">
                <!-- <div class="close-btn btn" @click="closeBattle"> <span>CLOSE</span> </div> -->
                <!-- <div class="log-card-switch-btn btn" @click="switchDisplay" v-if="!enemyWon && !playerWon">{{infoDisplayMode}}</div> -->
                
                <!-- <div class="skills-btn btn" v-if="!enemyWon && !playerWon">SPELLS</div> -->
                <div class="continue-to-end-game end-game-btns btn"  @click="restartDebug" >Restart debug</div>
                <div class="continue-to-end-game end-game-btns btn"  @click="closeDebug" >Close debug</div>
                
               
                
            </div>
    </div>
</template>

<script>
import {gameStore} from '@/stateManagement/gameStore.js';
import {setTimeout} from 'timers';
import {unitsOfOrder} from '@/stateManagement/gameStore.js';



export default {
    data() {
        return {
            playerUnits: [],
            playerUnitsSecondWave: [],
            enemyUnits: [],
            infoDisplayMode: 'INFO CARD',
            displayCard: false,
            playerWon: false,
            enemyWon: false,
            
            updateBattleEventLog:[],
            duringPlayerMove: false,

            playerUnit1: undefined,
            playerUnit2: undefined,
            playerUnit3: undefined,
            playerUnit4: undefined,
            playerUnit5: undefined,
            playerUnit6: undefined,
            playerUnit7: undefined,
            playerUnit8: undefined,

            enemyUnit1: undefined,
            enemyUnit2: undefined,
            enemyUnit3: undefined,
            enemyUnit4: undefined,
            enemyUnit5: undefined,
            enemyUnit6: undefined,
            enemyUnit7: undefined,
            enemyUnit8: undefined,

            selectModeActive: false,
            initiativeQ: [],
            unitsToMove: [],
            unitsMoved: [],
            
        }
    },

    methods: {
        closeBattle() {
            this.$emit('closeBattleScreen');
        },
        switchDisplay() {
            this.displayCard = !this.displayCard;
            if (this.displayCard) {
                this.infoDisplayMode = "BATTLE LOG";
            } else {
                this.infoDisplayMode = "INFO CARD";
            }
        },
        restartDebug() {
            this.addUnits();
            this.startBattle();
        },
        updateScroll() {
            let scroll = this.$el.querySelector('.scroll');
            scroll.scrollTop = scroll.scrollHeight;
            
        },
        deathUpdate() {
            if (this.playerUnit1 && this.playerUnit1.hp <= 0 ) {
                let index = this.playerUnits.indexOf(this.playerUnit1);
                this.playerUnits.splice(index, 1);
                this.playerUnit1 = undefined;
            }
            if (this.playerUnit2 && this.playerUnit2.hp <= 0 ) {
                let index = this.playerUnits.indexOf(this.playerUnit2);
                this.playerUnits.splice(index, 1);
                this.playerUnit2 = undefined;
            }
            if (this.playerUnit3 && this.playerUnit3.hp <= 0 ) {
                let index = this.playerUnits.indexOf(this.playerUnit3);
                this.playerUnits.splice(index, 1);
                this.playerUnit3 = undefined;
            }
            if (this.playerUnit4 && this.playerUnit4.hp <= 0 ) {
                let index = this.playerUnits.indexOf(this.playerUnit4);
                this.playerUnits.splice(index, 1);
                this.playerUnit4 = undefined;
            }
            if (this.playerUnit5 && this.playerUnit5.hp <= 0) {
                let index = this.playerUnits.indexOf(this.playerUnit5);
                this.playerUnits.splice(index, 1);
                this.playerUnit5 = undefined;
            }
            if (this.playerUnit6 && this.playerUnit6.hp <= 0 ) {
                let index = this.playerUnits.indexOf(this.playerUnit6);
                this.playerUnits.splice(index, 1);
                this.playerUnit6 = undefined;
            }
            if (this.playerUnit7 && this.playerUnit7.hp <= 0 ) {
                let index = this.playerUnits.indexOf(this.playerUnit7);
                this.playerUnits.splice(index, 1);
                this.playerUnit7 = undefined;
            }
            if (this.playerUnit8 && this.playerUnit8.hp <= 0 ) {
                let index = this.playerUnits.indexOf(this.playerUnit8);
                this.playerUnits.splice(index, 1);
                this.playerUnit8 = undefined;
            }
            if (this.enemyUnit1 && this.enemyUnit1.hp <= 0 ) {
                let index = this.enemyUnits.indexOf(this.enemyUnit1);
                this.enemyUnits.splice(index, 1);
                this.enemyUnit1 = undefined;
            }
            if ( this.enemyUnit2 && this.enemyUnit2.hp <= 0 ) {
                let index = this.enemyUnits.indexOf(this.enemyUnit2);
                this.enemyUnits.splice(index, 1);
                this.enemyUnit2 = undefined;
            }
            if (this.enemyUnit3 && this.enemyUnit3.hp <= 0 ) {
                let index = this.enemyUnits.indexOf(this.enemyUnit3);
                this.enemyUnits.splice(index, 1);
                this.enemyUnit3 = undefined;
            }
            if (this.enemyUnit4 && this.enemyUnit4.hp <= 0 ) {
                let index = this.enemyUnits.indexOf(this.enemyUnit4);
                this.enemyUnits.splice(index, 1);
                this.enemyUnit4 = undefined;
            }
            if (this.enemyUnit5 && this.enemyUnit5.hp <= 0 ) {
                let index = this.enemyUnits.indexOf(this.enemyUnit5);
                this.enemyUnits.splice(index, 1);
                this.enemyUnit5 = undefined;
            }
            if (this.enemyUnit6 && this.enemyUnit6.hp <= 0 ) {
                let index = this.enemyUnits.indexOf(this.enemyUnit6);
                this.enemyUnits.splice(index, 1);
                this.enemyUnit6 = undefined;
            }
            if (this.enemyUnit7 && this.enemyUnit7.hp <= 0 ) {
                let index = this.enemyUnits.indexOf(this.enemyUnit7);
                this.enemyUnits.splice(index, 1);
                this.enemyUnit7 = undefined;
            }
            if (this.enemyUnit8 && this.enemyUnit8.hp <= 0 ) {
                let index = this.enemyUnits.indexOf(this.enemyUnit8);
                this.enemyUnits.splice(index, 1);
                this.enemyUnit8 = undefined;
            }
            //check for dead units in order not to resurrect them and not make error of classlist of undefined
            this.unitsMoved.forEach ( unit => {
                    if (unit.hp <= 0) {
                        let index = this.unitsMoved.indexOf(unit);
                        this.unitsMoved.splice(index,1);
                    }
            })
        },
        async startBattle() {
            
            this.checkForVictory();
            if (this.playerWon) {
                return;
            }
            
            // stop calling startBattle() if this is the player move
            if (this.duringPlayerMove) {
                return;
            }
            //might be useful if there is a unit with passive aoe dmg every turn, which means stacks might get 0 or less hp during next turn
            this.checkStacksForDeadUnits();
            
            //if the owner is the enemy then run the AI movement function here
            
            //start moving units from the 1st one in the initiative stack
            
            if ((this.unitsToMove[0].owner === "enemy")) {
                if (JSON.stringify(this.unitsToMove) !=='[]'&& this.unitsToMove[0].owner === "enemy") {
                this.$el.querySelector('.' + this.unitsToMove[0].owner +'-unit-' + this.unitsToMove[0].position).classList.add('active-enemy-unit-border');
            }
                await this.enemyMoveFunction();
            }
             
            //Player Action Function (below currently)
            // if the owner is the player, run the action function
            // in action function: activate select mode for that unit
            
            
             if (( this.unitsToMove[0].owner === "player")) {
                 
                if (JSON.stringify(this.unitsToMove) !=='[]' && this.unitsToMove[0].owner === "player") {
                this.$el.querySelector('.player-unit-' +this.unitsToMove[0].position).classList.add('active-player-unit-border');
            }
                this.selectModeActive = true;
                this.duringPlayerMove = true;
            }

            this.checkForVictory();
            if (!this.playerWon) {
                this.startBattle();
            }
                
        },
        checkForNextTurn() {
            if (this.unitsToMove.length === 0 ) {
                this.newTurn();
                this.updateLog('Next turn starts.');
            }
        },
        passiveAbilities() {
            let self = this;
            this.unitsMoved.forEach(unit => {
                //resolve poison
                if (unit.poisonTicks > 0) {
                    self.$el.querySelector('.' + unit.owner +'-unit-' + unit.position+'-hit-anim').classList.add('poison-tick');
                    
                    
                    
                    setTimeout(() => {
                    this.$el.querySelector('.' + unit.owner +'-unit-' + unit.position+'-hit-anim').classList.remove('poison-tick');
                    
                    }, 300)
                    unit.hp -= unit.poisonValue;
                    unit.poisonTicks -=1;
                    this.updateLog(unit.name +" takes " +unit.poisonValue + " damage from poison.")
                   
                    
                }
                //resolve regenerarion
                unit.hp += unit.regeneration;
                if (unit.hp > unit.maxHp) {
                    unit.hp = unit.maxHp;
                }
                if(unit.regeneration > 0) {
                    this.updateLog(unit.name +" regenerates " +unit.regeneration + " hit points.")
                }
            })
            this.checkStacksForDeadUnits();
        },
        attackFunction(target) {
            if (this.selectModeActive === true) {
            this.selectModeActive = false;
            this.damageFunction(this.unitsToMove[0], target)
            this.deathUpdate();
            
            // remove the green border around the player unit
            this.$el.querySelectorAll('.player-unit').forEach ((el) => {
                        el.classList.remove('active-player-unit-border');
                    })
            this.unitsMoved.push(this.unitsToMove[0])
            this.unitsToMove.shift();
            this.duringPlayerMove = false;
            this.checkForNextTurn();
                this.startBattle();
            }
           
            
        },
        enemyMoveFunction() {
            //current enemy unit attacks 1st row until the unit is dead, then moves to another
            //for now that's all the enemy AI does
            return new Promise (resolve => {
                
                let self = this;
                 if (JSON.stringify(this.unitsToMove[0]) !=='[]' && this.unitsToMove[0].owner === "enemy") {
                this.$el.querySelector('.' + this.unitsToMove[0].owner +'-unit-' + this.unitsToMove[0].position).classList.add('active-enemy-unit-border');
            }
                setTimeout(function() {
                    
                    //here will be the enemy target selection code
                    //for now it's just a loop through enemy units and hitting the 1st one found
                    if (self.unitsToMove[0].owner === "enemy" && self.playerUnits.length > 0) {
                        for (let i = 0;i < self.playerUnits.length; i++) {
                            
                            // check for absorption? if (playerUnits[i].absorpiton > self.unitsToMove[0].dmg) {exclude this unit from attacking unless it's the last one}
                             if ( self.playerUnits[i].hp > 0) {
                                
                                self.damageFunction(self.unitsToMove[0], self.playerUnits[i]);
                                self.deathUpdate();
                                break;
                            }
                        }
                    
                    //remove the active border
                    
                    self.$el.querySelectorAll('.enemy-unit').forEach ((el) => {
                        el.classList.remove('active-enemy-unit-border');
                    })
                    //reset self.activeUnit if necessary
                } else {
                    //fight ends, no more player untis to hit, player loses
                        setTimeout(function() {
                            self.enemyWon = true;
                            
                        }, 500)
                        
                       return;
                }
                    self.unitsMoved.push(self.unitsToMove[0]);
                    //remove from initiative queue
                    self.unitsToMove.shift();
                    self.checkForNextTurn();
                    self.updateScroll();
                    
                    
                    resolve("enemyMoveFunction")
                }, 700) 
                
            })
        },
        damageFunction(attacker, target) {
            let self = this;
            
            //apply animation
            this.$el.querySelector('.'+target.owner +"-unit-"+target.position+'-hit-anim').classList.add('hit-anim');
            setTimeout(function() {
                if (self.$el.querySelector('.'+target.owner +"-unit-"+target.position+'-hit-anim')){
                    self.$el.querySelector('.'+target.owner +"-unit-"+target.position+'-hit-anim').classList.remove('hit-anim');
                } else if (self.$el.querySelector('.placeholder-'+target.owner+'-'+target.position)) {
                    self.$el.querySelector('.placeholder-'+target.owner+'-'+target.position).classList.add('hit-death');
                }             
            },300)
            //resolve damage
            //deal dmg helper function
            // calculates dmg considering absorption, prevents healing from negative dmg value if absorption is greater than dmg
            function dealDmg() {
                
                if ((attacker.dmg - target.absorption) > 0 ) {
                    
                    return attacker.dmg - target.absorption;
                    } else {
                    // minimal value you can deal dmg for, it is impossible for absorption to absorb 100% of the blow
                    // that means you always deal 1 dmg, no matter the target absorption, thus return 1
                        return 1;
                    }
                }
            
            target.hp -= dealDmg();
            
            if (target.hp < 0) {
                target.hp = 0;
            }
            
            this.updateLog(target.name + ' is hit for ' +dealDmg() + ' by the ' + attacker.name +'.');
            
            if (target.absorption >0) {
                this.updateLog(target.name+' absorbs ' + (target.absorption) + ' damage!')
            }
            //apply poison here if the attacker has poison
            if (attacker.poisonDuration > 0) {
                if (!target.poisonTicks || target.poisonTicks < attacker.poisonDuration) {

                    target.poisonTicks = attacker.poisonDuration;
                    
                    
                }
                if (!target.poisonValue || target.poisonValue < attacker.poisonDmg) {
                    
                    target.poisonValue = attacker.poisonDmg;
                    
                    
                }
                
            this.updateLog(target.name + ' is poisoned by the '+attacker.name+'!');
            
            }
                
            if (target.hp <= 0) {
                let index = this.playerUnits.indexOf(target);
                this.checkStacksForDeadUnits();
                this.updateLog(target.name + ' is killed!');
                if (target.owner === 'player' && this.playerUnitsSecondWave.length > 0) {
                    
                    let newPosition = 'playerUnit' + target.position;
                    this.playerUnits.splice(index, 0, this.playerUnitsSecondWave[0]);
                    
                    this.playerUnitsSecondWave.shift();
                    this.unitsToMove.push(this.playerUnits[index]);
                    this.playerUnits[index].position=target.position;
                    
                    this[newPosition] =  this.playerUnits[index];
                                
                    this.updateLog("One of your units, " + this.playerUnits[index].name + ', enters the battle from reserves in the place of the fallen ' + target.name + '!')
                    
                }
            }
            
        },
        checkStacksForDeadUnits() {
            this.deathUpdate();
            this.unitsToMove = this.unitsToMove.filter(unit => {
                    return unit.hp > 0;
                })
            this.unitsMoved = this.unitsMoved.filter(unit => {
                    return unit.hp > 0;
                })
            this.playerUnits = this.playerUnits.filter(unit => {
                    return unit.hp > 0;
                })
            
                
        },
        newTurn() {
                if (this.unitsToMove.length === 0 ) {
                //turn effects like regeneration go here, create a separate function for this
                this.passiveAbilities();
                this.unitsToMove = this.unitsMoved;
                this.unitsMoved = [];
                }
                
        },
        checkForVictory() {
            
            if (this.enemyUnit1===undefined && this.enemyUnit2===undefined && this.enemyUnit3===undefined && this.enemyUnit4===undefined && this.enemyUnit5===undefined && this.enemyUnit6===undefined && this.enemyUnit7===undefined && this.enemyUnit8===undefined) {
                this.checkStacksForDeadUnits();
                this.playerUnits.forEach(unit => {
                    unit.hp = unit.maxHp;
                    })
                this.playerWon = true;
                this.updateBattleEventLog.push('You defeat the forces of order! The battle is won! You can continue your experiments undisturbed... for now.')
                
            } 
        },
        gameOver() {
            this.$emit('gameOver');
        },
        battleWon() {
            this.checkStacksForDeadUnits();
                      
            gameStore.playerCharacter.creatures = this.playerUnits;
            
            if (this.playerUnitsSecondWave.length>0) {
            gameStore.playerCharacter.creatures.push(...this.playerUnitsSecondWave);
            }
            
            this.$emit('playerWins');
        },
        updateLog(msg) {
            let self = this;
            this.updateBattleEventLog.push(msg);
            setTimeout(() => {
                self.updateScroll();
            }, 100);
            
        },
        sortUnits(owner) {
        let own = owner+"Units";
        this[own].sort( (a, b) => {
            if (a.position === b.position) {
                a.position++;
                if(a.position> 8) {
                    a.position = 8;
                }
            }
            return b.position - a.position;
        })
    },
    assignPositions: function(owner){
            let self = this; 
            //position 9 guard - if a unit is assigned position 9 or higher it falls out of the unit group
            function position9guard(unit) {
                if (unit.position>=9) {
                                let index = self[ownUs].indexOf(unit);
                                self[ownUs].splice(index, 1);
                                self.playerUnitsSecondWave.push(unit);
                            }
            }
            let ownUs = owner+"Units";
            let ownU = owner+"Unit";
            this[ownUs].forEach( (unit) => {
                for (let i =1; i<= 8; i++) {
                    
                    let currentUnitPos = ownU+i;
                    position9guard(unit);
                    if (unit.position === i) {
                        if (!this[currentUnitPos]){
                            this[currentUnitPos] = unit;
                            
                        } else {
                            unit.position++;
                            position9guard(unit);
                            
                        }
                        
                    }
                    
                }
                
            })
        },
        addUnits() {
             for (let i = 0; i < 3; i++ ) {
            let unit = unitsOfOrder.createKnight();
            this.enemyUnits.push(unit);
        }
        for (let i = 0; i <5; i++) {
            let unit = unit = unitsOfOrder.createAssassin();
            this.enemyUnits.push(unit);          

        }
            let vampire = new unitsOfOrder.UnitOfOrder("Vampire", 280, 280, 35, 'vampire-girl.svg', 35, 'player', 12, 14, 230, 1);
            console.log(vampire)
            this.playerUnits.push(vampire);
        }

    },
    
    computed: {
        activeUnit() {
            if (this.unitsToMove.length === 0) {
                return this.unitsMoved[0];
            } else {
                return this.unitsToMove[0];
            }
        }
    },
    
    created() {
        // let self = this;
        this.addUnits();
        
        
        this.sortUnits("enemy");
        this.updateBattleEventLog.push("Angry townspeople are at your gates, undoubtedly to end good doctor's life. Let's hope your monsters are strong enough...");  
        //import player units and the enemy units
        
        // if (gameStore.playerCharacter.creatures.length === 0) {
        //     this.updateLog("Alas! You have no creatures to defend yourself! What an ill, unjust fate for a good doctor! The townspeople see to it that justice is done here and now...");
        //      setTimeout(function() {
        //          self.enemyWon = true;
                        
        //             }, 500)
            
        // }
        //check if there is more than 8 creatures
        // if (gameStore.playerCharacter.creatures.length >8) {
        //     let unitsAbove8 = gameStore.playerCharacter.creatures.slice(8, gameStore.playerCharacter.creatures.length);
        //     this.playerUnitsSecondWave.push(...unitsAbove8);
        // }
        // this.playerUnits = gameStore.playerCharacter.creatures.slice(0, 8);
        
        // populate the grids with units (as set up in creations component, if the player didn't set up, it is done here by default)
        this.sortUnits('player')
        this.assignPositions('player');
        this.assignPositions('enemy');
        //declare initiative queue
            
        // get initiative from player units and enemy units and put it into the queue

        let allUnits =[...this.playerUnits, ...this.enemyUnits];
        allUnits.sort((a, b)=> {return b.initiative - a.initiative})
        this.unitsToMove = allUnits;
    },
    mounted() {
        this.startBattle();
    }
}
</script>

<style scoped>

.battle-screen {
    background: #1c1c1c;
    color: #fff;
    height: 100vh;
    width: 100%;
    position: absolute;
    display: grid;
    align-content: center;
    z-index: 2;
    align-content: space-evenly;
    overflow-y: hidden;
}

/* event log styles */

.log-and-unit-card-container {
    height: 120px;
    margin-bottom: .5rem;
}
.event-log {
    background: #d8cfba;
    color: #000;
    height: 120px;
    padding: 1rem;
    display: grid;
    -webkit-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    -moz-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
}

.event-log-title {
    font-size: 1.2rem;
    line-height: 1.4rem;
}

.event-log-info-list {
    overflow-y: auto;
}



/* battle grid */

.battle-display-grid {
    display: grid;
    grid-gap: .5rem;
    margin: .4rem;

}

.enemy-units-grid, .player-units-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr 1fr;
    grid-gap: .3rem;
}

.unit img {
    width: 100%;
    max-width: 100%;
    height: auto;
}

.unit-placeholder {
    background: #262424;
    border: 1px solid #000;
    position: relative;

}

.division {
    height: 1px;
    background: #000;
}

/* hp display on the card */

.unit {
    position: relative;
}

.hp-display {
    bottom: 0;
    right: 0;
    position: absolute;
    border-radius: 50%;
    background: red;
    color: #000;
    width: 1.5rem;
    height: 1.5rem;
    text-align: center;
    display: grid;
    font-weight: 600;
}

/* active card styles*/

.active-unit-card {
    display: grid;
    grid-template-columns: 1fr 2fr;
}

.active-unit-card img {
    width: 100%;
    max-width: 100%;
    height: auto;
}

.stat-block-card {
    align-items: center;
    display: grid;
    grid-template-columns: 1fr 1fr;
    -webkit-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    -moz-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    background: #D8CFBA;
    color: #000;
    padding: .5rem;
    
}

.active-enemy-unit-border {
    border: 1px solid red;
}
.active-player-unit-border {
    border: 1px solid green;
}

/* buttons styles */

.buttons-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
}


.btn {
    margin: .4rem;
    padding: .5rem 3rem;
    background-color: #262424;
    border: #000 1px solid;
    cursor: pointer;
    cursor: pointer;
    text-align: center;
    display: grid;
    align-content: center;
    
}

.end-game-btns {
    grid-column: span 2;
}

.hit-anim {
    height: 100%;
    width: 100%;
    max-width: auto;
    background: url('../assets/icons/slash-transparent.svg') no-repeat;
    animation: hitAnimation .3s forwards;
    position: absolute;
    top: 0;
    right: 0;
}

.poison-tick {
    height: 100%;
    width: 100%;
    max-width: auto;
    background: url('../assets/icons/poison-transparent.svg') no-repeat;
    animation: poisonAnimation .3s forwards;
    position: absolute;
    top: 0;
    right: 0;
}
.hit-death {
    height: 100%;
    width: 100%;
    max-width: auto;
    background: url('../assets/icons/dead-ico.svg') no-repeat;
    animation: deathAnimation .3s;
    position: absolute;
    top: 0;
    right: 0;
}

/* keyframes hit animation*/
@keyframes hitAnimation {
    0% {
        opacity: 0;
        transform: rotate(0);
        margin: 0;
        
    } 50% {
        opacity: 1;
        transform: rotate(45deg);
        margin-left: 15px;
        margin-top: 12px;
        
        
    } 100% {
        opacity: 0;
        transform: rotate(60deg);
        margin: 0;
    }
}

@keyframes poisonAnimation {
    0% {
        opacity: 0;
    } 50% {
        opacity: 1;
    } 100% {
        opacity: 0;
    }
}

@keyframes deathAnimation {
    from {
        opacity: 0;
    } to {
        opacity: 1;
    } 
    
}

</style>
