<template>
    <div class="shop-component">
        <div class="shop-name">
            <h2>SHOP</h2>
            <h3 class="buy-items">Buy items</h3>
        </div>
        <div class="player-gold">
            <h3>Gold: {{playerGold}}</h3>
        </div>
        <div class="shop-items-grid">
            <h3>Available items:</h3>
            
            <item v-for="(item, index) in itemsList" :key="index" :item="item" @click.native="selectItem(item)" :price="item.cost">
                
            </item>

        </div>

        <div class="selected-item">
            <h3>Selected item:</h3>
            <item :item="selectedItem" v-if="itemSelected"></item>
            <div class="no-item-selected-text" v-if="!itemSelected">
                Select an item to buy
            </div>
            <div class="gold-cost-display">
                Cost: {{currentItemCost}}
            </div>
        </div>
        
        <div class="shop-buttons">
            <div class="buy-button buy btn" @click="addItemToInventory">
                BUY
            </div>

            <div class="close-button btn" @click="closeShop">
                RETURN
            </div>
        </div>
        <!-- info display modal -->
            <transition name="show-modal">
        <div class="info-modal" v-if="displayInfo">
            <p>{{this.message}}</p> 
        </div>
            </transition>
    </div>
    
</template>

<script>
import item from '@/components/itemComponent.vue';
// import {gameStore} from '@/stateManagement/gameStore.js';

export default {
    components: {
        item
    },
    props: ['itemsList', 'goldAmount'],
    data() {
        return {
            selectedItem: {},
            displayInfo: false,
            message: '',
            currentItemCost: 0,
            boughtItemsValue: 0

        }
    },
    methods: {
        closeShop() {
            this.$emit('closeShop', this.playerGold );
        },
        selectItem(item) {
            
            this.selectedItem = item;
            this.updateCostDisplayed();
        },
        addItemToInventory() {

            if ( (this.playerGold - this.selectedItem.cost) >= 0 ) {

            this.$emit('addItemToInventory', this.selectedItem);
            this.displayInfoFn("Item bought.");
            this.boughtItemsValue += this.selectedItem.cost;
            this.selectedItem = {};
            this.updateCostDisplayed();

            } else if (JSON.stringify(this.selectedItem) === "{}") {
                this.displayInfoFn("Select an item to buy!")
            } else {
                this.displayInfoFn("You can't afford this item!");
                return;
            }
            
            
            
            
        },
        displayInfoFn(msg) {
            this.displayInfo = true;
            this.message = msg;
            setTimeout ( () => {
                this.displayInfo = false;
            }, 900)
        },
        updateCostDisplayed() {
            if (JSON.stringify(this.selectedItem) !=='{}') {
                this.currentItemCost = this.selectedItem.cost;
            } else {
                this.currentItemCost = 0;
            }
           
        },
        
    },
    computed: {
        itemSelected() {
            if (JSON.stringify(this.selectedItem) ==='{}') {
                return false;
            } else {
                return true;
            }
        },
        playerGold() {
            return this.goldAmount - this.boughtItemsValue;
        }
        
    }

}
</script>

<style scoped>
.shop-component {
    background: #1c1c1c;
    color: #fff;
    height: 100vh;
    width: 100%;
    position: absolute;
    display: grid;
    justify-content: center;
    align-content: center;
    z-index: 3;
    padding: .5rem;
    }

    .shop-name h2 {
        text-align: center;
    }

    .shop-name .buy-items {
        text-align: center;
    }

   

    /* shop grid */

    .shop-items-grid {
        display: grid;
        justify-content: center;
        
        overflow-y: auto;
    }

    .shop-buttons {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }

    /* info modal transition*/

.show-modal-enter {
    opacity: 0;
}
.show-modal-to {
    opacity: 1;
}
.show-modal-leave-to {
    opacity: 0;
}

.info-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 1.3rem;
    padding: 2rem;
    background: rgba(0, 0, 0, .8);
    border-radius: 10px;
    width: 80%;
    text-align: center;
    transition: all .7s ease-in-out;
    display: grid;
     
    }

    /* buttons */

    .btn {
    margin: .4rem;
    padding: .5rem 3rem;
    background-color: #262424;
    border: #000 1px solid;
    cursor: pointer;
    cursor: pointer;
    text-align: center;
}
</style>
