<template>

<div class="character-creation">
    <div class="character-creation-container">

    <div class="character-sheet">
            <div class="character-sheet-title"> <h3>Character sheet</h3> </div>
        <div class="character-sheet-columns-wrapper">

            <div class="character-sheet-column-1">
                <div class="character-sheet-name">Name: {{playerCharacter.name}}</div>
                <div class="character-sheet-lev">Level: {{playerCharacter.level}}</div>
                <div class="character-sheet-experience">Experience: {{playerCharacter.experience}}</div>
                <div class="character-sheet-nxtLvlExp">Experience for next level: {{playerCharacter.experienceForNextLevel()}}</div>
                

                <div class="character-sheet-statistics">
                    <ul class="statistics-ul">
                        <li><h4>Statistics:</h4> </li>
                        <li>Endurance: {{playerCharacter.statistics.Endurance}} </li>
                        <li>Intelligence: {{playerCharacter.statistics.Intelligence}}</li>
                        <li>Luck: {{playerCharacter.statistics.Luck}}</li>
                        <li>Magic find: {{playerCharacter.magicFind()}} </li>
                        <li>Action points: {{playerCharacter.calculateActionPoints()}}</li>
                        
                    </ul>
                </div>
            </div>

            <div class="character-sheet-column-2">
                <div class="skills-wrapper">
                    <div class="skills-display-name"><h4>Skill effects:</h4> 
                        <ul class="skill-list-sheet">
                            <li v-for="(skill, index) in playerCharacter.skillsSelected" :key="index">{{skill.effectDesc}}</li>
                        </ul>
                    </div>
                </div>
                
                    <ul class="skills-list">
                        <!-- add selected skills? -->
                    </ul>
                
                <div class="inventory-sheet-display">
                    <h4>Inventory:</h4>
                    <p>Gold: {{playerCharacter.gold}}</p>
                    <div class="inventory-sheet-list">
                            <div class="inventory-heads-wrapper" v-if="JSON.stringify(playerCharacter.inventory.craftingParts.heads)!=={}">
                                <ul class="crafts-inventory-list" v-for="(head, index) in playerCharacter.inventory.craftingParts.heads" :key="index" >
                                    <li>{{head.name}}</li>
                                </ul>
                                </div>
                        
                            <div class="inventory-arms-wrapper" v-if="JSON.stringify(playerCharacter.inventory.craftingParts.arms)!=={}">
                                    <ul class="crafts-inventory-list" v-for="(arm, index) in playerCharacter.inventory.craftingParts.arms" :key="index" >
                                    <li>{{arm.name}}</li>

                                </ul>
                            </div>

                            <div class="inventory-hearts-wrapper" v-if="JSON.stringify(playerCharacter.inventory.craftingParts.hearts)!=={}">
                                <ul class="crafts-inventory-list" v-for="(heart, index) in playerCharacter.inventory.craftingParts.hearts" :key="index" >
                                    <li>{{heart.name}}</li>

                                </ul>
                                </div>

                            <div class="inventory-torsos-wrapper" v-if="JSON.stringify(playerCharacter.inventory.craftingParts.torsos)!=={}">
                                <ul class="crafts-inventory-list" v-for="(torso, index) in playerCharacter.inventory.craftingParts.torsos" :key="index" >
                                        <li>{{torso.name}}</li>

                                </ul>
                                </div>

                            <div class="inventory-legs-wrapper" v-if="JSON.stringify(playerCharacter.inventory.craftingParts.legs)!=={}">
                                <ul class="crafts-inventory-list" v-for="(leg, index) in playerCharacter.inventory.craftingParts.legs" :key="index" >
                                        <li>{{leg.name}}</li>

                                </ul>
                            </div>

                            <div class="inventory-brains-wrapper" v-if="JSON.stringify(playerCharacter.inventory.craftingParts.brains)!=={}">
                                <ul class="crafts-inventory-list" v-for="(brain, index) in playerCharacter.inventory.craftingParts.brains" :key="index" >
                                    <li>{{brain.name}}</li>

                                </ul>
                            </div>

                            <div class="inventory-necbooks-wrapper" v-if="JSON.stringify(playerCharacter.inventory.necroBooks)!=={}">
                                <ul class="crafts-inventory-list" v-for="(nBook, index) in playerCharacter.inventory.necroBooks" :key="index" >
                                    <li>{{nBook.name}}</li>

                                    </ul>
                            </div>

                            <div class="inventory-necbooks-wrapper" v-if="JSON.stringify(playerCharacter.inventory.necroRecipes)!=={}">
                                <ul class="crafts-inventory-list" v-for="(nRecipe, index) in playerCharacter.inventory.necroRecipes" :key="index" >
                                    <li>{{nRecipe.name}}</li>

                                </ul>
                            </div>  
                    </div>
                </div>
            </div>
        </div>
        
    </div>

        <div class="character-buttons">
            <ul class="buttons-list">
                <li class="char-name-btn btn" @click="typeName">NAME</li>
                <li class="char-name-btn btn" @click="distributeStats">STATISTICS</li>
                <li class="char-name-btn btn" @click="selectSkills">SKILLS</li>
                <li class="char-name-btn btn" @click="openShopFn">ITEMS</li>
            </ul>
        </div>

    
    </div>

    <!-- character setup modals -->

    <!-- modal to enter the name -->
    <div class="name-entry" v-if="nameDisplay">
        <div class="name-entry-container">
            <div class="name-display-row">
                <div class="name-display">Choose a name:</div>
                
            <input type="text" v-model="playerCharacter.name">
            </div>
            <div class="close-btn btn" @click="close">
                DONE
            </div>
            
        </div>
    </div>
    <!-- modal to enter the statistics -->

    <div class="statistics-distribution" v-if="statsDisplay">
        <h2>DISTRIBUTE STATISTIC POINTS</h2>
        <div class="skills-explanation">
            <p>Endurance determines how much <span class="highlighted">Action Points</span> you have available per turn. For every 3 points of endurance you get an <span class="highlighted">Action Point</span>.</p>
            <p>Intelligence determines how much <span class="highlighted">Skill Points</span> you can spend on your skills. You get one <span class="highlighted">Skill Point</span> per 3 intelligence points.</p>
            <p>Luck governs the <span class="highlighted">Magic Find</span> parameter, determining how much and how good items you will get by digging up graves.</p>
        </div>
        <h2> Points left: {{playerCharacter.statisticsPointAllowance}} </h2>
        <div class="statistics-list-grid">
            <ul class="statistics-ul-modal">
                <li>Endurance: <span class="current-endurance">{{playerCharacter.statistics.Endurance}}</span> <span class="increase-endurance distribute-stat" @click="increaseStat('Endurance')">+</span><span class="decrease-endurance distribute-stat" @click="decreaseStat('Endurance')">-</span> </li>
                <li>Intelligence: <span class="current-intelligence">{{playerCharacter.statistics.Intelligence}}</span> <span class="increase-intelligence distribute-stat" @click="increaseStat('Intelligence')">+</span><span class="decrease-intelligence distribute-stat" @click="decreaseStat('Intelligence')">-</span></li>
                <li>Luck: <span class="current-luck">{{playerCharacter.statistics.Luck}}</span> <span class="increase-luck distribute-stat" @click="increaseStat('Luck')">+</span><span class="decrease-luck distribute-stat" @click="decreaseStat('Luck')">-</span></li>
            </ul>
            
        </div>

        <div class="close-name-display-btn btn" @click="close">
                DONE
            </div>
    </div>

    <!-- modal to select the skills -->

    <div class="skill-selection" v-if="skillsDisplay">
        <h1>SELECT SKILLS</h1>
        <p>Number of skill points depends on intelligence statistic</p>
        <h3>Available skill points: <span>{{calcSkillPoints}}</span></h3>

        <div class="skill-display-grid">
            <div class="skill-item" v-for="skill in playerCharacter.skills" :key="skill.number" @click="selectSkill(skill)">
                <div class="skill-name"> <h3>{{skill.name}}</h3> </div>
                <div class="skill-description">{{skill.description}}</div>
                <div class="skill-explanation"><span class="highlighted">{{skill.effectDesc}}</span></div>
            </div>
        </div>
        <div class="chosen-skills-display">
            <h3>Chosen skills:</h3>
            <ul class="chosen-skills-list">
                <li v-for="(skill, index) in playerCharacter.skillsSelected" :key="index">{{skill.name}} ({{skill.effectDesc}})</li>
            </ul>
        </div>
        <div class="btns-container">
            
            <div class="close-name-display-btn btn" @click="close">DONE</div>
            <div class="close-name-display-btn btn" @click="resetSkills">RESET</div>
        </div>
        

    </div>
    <!-- pre-game shop modal -->

    <shop-component class="first-shop" v-if="openShop" @closeShop="updateFromShop($event)" @addItemToInventory="addBoughtItem($event)" :itemsList="availableItems" :goldAmount="playerCharacter.gold"></shop-component>
    
   
   <!-- buttons section -->
    <div class="btns-container">
        <div class="done-creation btn" @click="finishCreationCheck">ACCEPT</div>
        <div class="back-btn btn" @click="backToMenu">BACK</div>
    </div>

    <!-- info display modal -->
            <transition name="show-modal">
            <div class="info-modal" v-if="displayInfo">
                <p>{{this.message}}</p> 
            </div>
            </transition>
            
            <div class="confirmation-modal" v-if="confirm">
                
                <h1>UNDISTRIBUTED POINTS</h1>
                <h1>{{this.message}}</h1>
                <div class="confirmation-btns" >
                    <div class="btn" @click="exportCharToStore">ACCEPT</div>
                    <div class="btn" @click="confirm=false">CANCEL</div>
                </div>
            </div>
</div>


    
</template>

<script>
import {gameStore} from '@/stateManagement/gameStore.js';
import shopComponent from '@/components/shopComponent.vue';
import { shop } from '@/stateManagement/itemsCollection.js';

export default {
    components: {
        shopComponent
    },
    data() {
        
        return {
        availableItems: shop.firstShopItems,
            
        playerCharacter: {
            hostilityLevel: 0,
            name: '',
            experience: 0,
            level: 1,
            skillsFromLevels:0,
            experienceForNextLevel() { 
                return this.level * 500},
            statistics: {
                Luck: 1,
                Intelligence: 1,
                Endurance: 1,
                
            },
            inventory: {
                craftingParts: {arms: [], legs: [], heads: [], torsos: [], brains: [], hearts: []},
                necroBooks: [],
                necroRecipes: [],
                demoRecipes: [],
                demoBooks: [],
            },
            ManOfAction: 0,
            MFbonus: 0,
            necromancyHPbonus: 0,
            demonologyHPbonus:0,
            automatonHPbonus: 0,
            automatonDamageBonus: 0,
            demonologyDamageBonus: 0,
            werewolvesHpBonus: 0,
            vampiresHpBonus: 0,
            skillManagement: {
                amountOfSelectedSkills: 0,
                manOfActionAdded: false,
                nutsAndBoltsAdded: false,
                hardWorkerAdded: false,
                creaturesOfTheNightAdded: false,
            },
            gold: 100,
            creatures: [],
            magicFind() {
                return this.statistics.Luck * 2 + this.MFbonus},
            calculateActionPoints() {
                let result = Math.floor(this.statistics.Endurance/3) ;
                if (result > 1) {
                    return result + this.ManOfAction;
                } else {
                    return 1 + this.ManOfAction;
                }
            },
            
            statisticsPointAllowance: 10,
            skillsSelected: [],
            skills: [{
            number: 1, name: 'Nuts and Bolts', description: "Sometimes magic alone can't hold the dacaying flesh together. Boost stats of your zombies by making them hold their form not only by magic, but also by screws, nuts and bolts.", effectDesc: "Necromancy creatures + 4 HP"},
            {number: 2, name: 'A Man of Action', description: "You are more spry than an average mad scientist! By having more energy to dig for corpses for half a night, or do errands all day, you gain 1 more action point.", effectDesc: "Action points increased by 1"},
            {number: 3, name: 'Hard Worker', description: "There must be something in the ground. At least something... A measly rotting leg... Maybe if I dig just a little bit longer...", effectDesc: "Magic Find increased by 15%"},            
            {number: 4, name: 'Creatures of the Night', description: "Bats and wolves, gather!", effectDesc: "Vampires and werewolves + 10 HP"},
            
            ]
            
        },

            displayInfo: false,
            confirm: false,
            message: '',
            
            nameDisplay: false,
            statsDisplay: false,
            skillsDisplay: false,
            openShop: false,
            
        }
    },
    methods: {
        typeName() {
            this.nameDisplay = true;
        },
        distributeStats() {
            this.statsDisplay = true;
        },
        selectSkills() {
            this.skillsDisplay = true;
            
            
            
        },
        openShopFn() {
            this.openShop = true;
        },
        close() {
            this.nameDisplay = false;
            this.statsDisplay = false;
            this.skillsDisplay = false;
            this.openShop = false;
            
        },
        backToMenu() {
            gameStore.gameState.isNewGame = false;                  
        },
        increaseStat(stat) {
            
            if (this.playerCharacter.statisticsPointAllowance > 0) {
                this.playerCharacter.statisticsPointAllowance--;
                this.playerCharacter.statistics[stat]++;
            } else {
                return;
            }
            
        },
        decreaseStat(stat) {
            if (stat === "Intelligence" && this.playerCharacter.skillManagement.amountOfSelectedSkills > 0) {
                
                this.displayInfoFn("The amount of available skill points changed! Your skill points have been reset!")
                this.resetSkills();
            }
             if (this.playerCharacter.statistics[stat] ==1)
            {
                return;
            }            
            this.playerCharacter.statistics[stat]--;
            this.playerCharacter.statisticsPointAllowance++;             
        },
        selectSkill(skill) {
            if (this.calcSkillPoints <= 0) {
                this.displayInfoFn("Not enough skill points.")
                return;
            } else if (this.playerCharacter.skillsSelected.includes( skill)) {
                this.displayInfoFn("You already have this skill")
            } else {
                this.playerCharacter.skillManagement.amountOfSelectedSkills ++;
                this.playerCharacter.skillsSelected.push(skill)
                this.playerCharacter.skillsSelected.forEach( (skill) => {
                if (skill.name.includes("A Man of Action") && !this.playerCharacter.skillManagement.manOfActionAdded){
                    this.playerCharacter.ManOfAction++;
                    this.playerCharacter.skillManagement.manOfActionAdded = true;
                    
                } else if (skill.name.includes("Nuts and Bolts") && !this.playerCharacter.skillManagement.nutsAndBoltsAdded) {
                    this.playerCharacter.necromancyHPbonus +=4;
                    this.playerCharacter.skillManagement.nutsAndBoltsAdded = true;
                } else if (skill.name.includes("Hard Worker") && !this.playerCharacter.skillManagement.hardWorkerAdded) {
                    this.playerCharacter.MFbonus +=15;
                    this.playerCharacter.skillManagement.hardWorkerAdded = true;
                } else if (skill.name.includes("Creatures of the Night") && !this.playerCharacter.skillManagement.creaturesOfTheNightAdded) {
                    this.playerCharacter.werewolvesHpBonus +=10;
                    this.playerCharacter.vampiresHpBonus +=10;
                    this.playerCharacter.skillManagement.creaturesOfTheNightAdded = true;
                }
            })
            }
            
            
        },
        resetSkills() {
            this.playerCharacter.skillManagement.amountOfSelectedSkills = 0;
            this.playerCharacter.skillsSelected = [];
            this.playerCharacter.ManOfAction = 0;
            this.playerCharacter.necromancyHPbonus = 0;
            this.playerCharacter.demonologyHPbonus = 0;
            this.playerCharacter.automatonHPbonus = 0;
            this.playerCharacter.werewolvesHpBonus = 0;
            this.playerCharacter.vampiresHpBonus = 0;
            this.playerCharacter.MFbonus = 0;
            this.playerCharacter.skillManagement.manOfActionAdded = false;
            this.playerCharacter.skillManagement.hardWorkerAdded = false;
            this.playerCharacter.skillManagement.creaturesOfTheNightAdded = false;
            this.playerCharacter.skillManagement.nutsAndBoltsAdded = false;
           
        },
        addBoughtItem($event) {
            
            if ($event.function === "craft") {
                this.playerCharacter.inventory.craftingParts[$event.type].push($event);
            } else if ($event.function === "necroBook") {
                
                this.playerCharacter.inventory.necroBooks.push($event);
                
            } else if ($event.function === "necroRecipes") {
                this.playerCharacter.inventory.necroRecipes.push($event);
            }
            
        },
        finishCreationCheck() {
            if(this.playerCharacter.statisticsPointAllowance > 0  ) {
                this.message = "You have undistributed statistic points. Are you sure you want to continue?"
                this.confirm = true;
                
            } else if (this.calcSkillPoints !== 0) {
                this.message = "You have undistributed skill points. Are you sure you want to continue?"
                this.confirm = true;

                
            } else {
                this.exportCharToStore();
            }
        },
        exportCharToStore() {
            
            gameStore.playerCharacter = this.playerCharacter;
            gameStore.gameState.characterCreated = true;
            
            
        },
        updateFromShop($event) {
            this.playerCharacter.gold = $event;
            this.openShop = false;
        },
        displayInfoFn(msg) {
            this.displayInfo = true;
            this.message = msg;
            setTimeout ( () => {
                this.displayInfo = false;
            }, 1000)
        }
    },
    computed: {
        calcSkillPoints() {
            return Math.floor(this.playerCharacter.statistics.Intelligence/3) + 1 - this.playerCharacter.skillManagement.amountOfSelectedSkills;
            },
    }

}
</script>

<style scoped>

/* background: #031926; 181d27*/


.character-creation {
    height: 100vh;
    display: grid;
    position: relative;
    grid-template-rows: 11fr 1fr;
    background: #1c1c1c;
    color: #fff;
}

.character-creation-container  {
    display: grid;
    
}

/*character sheet */

.character-sheet {
    background-color: #D8CFBA;
    color: #000;
    -webkit-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    -moz-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    padding: 1rem;
}

.character-sheet-columns-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

/* character creation selection buttons */

.character-buttons {
    display: grid;
    justify-items: center;
    align-content: center;
    text-align: center;
}

/* name entry */

.name-entry {
    display: grid;
    position: absolute;
    top: 0;
    width: 100%;
    justify-content: center;
    align-content: center;
    height: 100vh;
    z-index: 2;
    background: #1c1c1c;
    

}

.name-entry-container {
    display: grid;
    grid-template-rows: 2fr 1fr;
    justify-items: center;
    align-items: center;
}

.name-display {
    text-align: center;
    font-size: 2rem;
    margin: 1rem 1rem 0 1rem;
}

.name-entry input {
    background: #d8cfba;
    width: 100%;
    color: #000;
    font-size: 1.3rem;
    line-height: 1.7rem;
    padding: .5rem;
    margin: 1rem 0 .5rem 0;
}

/*stats entry */

.statistics-distribution {
    display: grid;
    position: absolute;
    top: 0;
    width: 100%;
    justify-content: center;
    align-content: center;
    height: 100vh;
    z-index: 2;
    background: #1c1c1c;
}

.skills-explanation {
    overflow-y: auto;
}

.statistics-distribution h2 {
    text-align: center;
    margin-bottom: 1rem;
}

.statistics-distribution p {
    text-align: justify;
    padding: 0 .5rem .5rem .5rem;
    line-height: 1.6rem;
    font-size: 1.1rem;
}

.statistics-list-grid {
    display: grid;
    
    justify-content: center;
}

.statistics-ul-modal li {
    line-height: 3rem;
    font-size: 2rem;
    display: grid;
    grid-template-columns: 3fr 1fr 1fr 1fr;
    justify-items: center;
}

.distribute-stat {
    background-color: #262424;
    border: #000 1px solid;
    padding: .4rem 1rem;
    margin-bottom: .5rem;
    cursor: pointer;
    

}

.close-name-display-btn {
    justify-self: center;
}

/* skill selection */

.skill-selection {
    display: grid;
    position: absolute;
    top: 0;
    width: 100%;
    justify-content: center;
    align-content: center;
    height: 100vh;
    z-index: 2;
    background: #1c1c1c;
    padding: .5rem;
}

.skill-selection h1 {
    text-align: center;
    
}

.skill-selection h1, p, h3 {
    padding: .5rem 0;
}

.skill-display-grid {
    overflow-y: auto;
}

.skill-item{ 
    background-color: #262424;
    border: #000 1px solid;
    padding: .5rem;
}

/* shop section */

.first-shop {
     display: grid;
    position: absolute;
    top: 0;
    width: 100%;
    justify-content: center;
    align-content: center;
    height: 100vh;
    z-index: 2;
    background: #1c1c1c;
}

.shop-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    width: 100%;
}

/* buttons */

.btns-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    
}

@media (min-width: 360px) {
    .btns-container .btn {
    margin: 1rem;
}
}

.btn {
    margin: .4rem;
    padding: .5rem 3rem;
    background-color: #262424;
    border: #000 1px solid;
    cursor: pointer;
    cursor: pointer;
    text-align: center;
}

.increase-ul li {
    cursor: pointer;
}

/* info modal transition*/

.show-modal-enter {
    opacity: 0;
}
.show-modal-to {
    opacity: 1;
}
.show-modal-leave-to {
    opacity: 0;
}

.info-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 1.3rem;
    padding: 2rem;
    background: rgba(0, 0, 0, .8);
    border-radius: 10px;
    width: 80%;
    text-align: center;
    transition: all .7s ease-in-out;
    display: grid;
    z-index: 3;
     
    }

.confirmation-modal {
    background: #1c1c1c;
    color: #fff;
    height: 100vh;
    width: 100%;
    position: absolute;
    display: grid;
    justify-content: center;
    align-content: center;
    z-index: 2;
    }

.confirmation-modal h1 {
    margin: 1rem;
    text-align: center;
    
    line-height: 3rem;
    padding: 1rem;
    }


.confirmation-btns {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: .5rem;
}

.highlighted {
  color: #ffb400;
}

</style>
