<template>
    <div class="item">
        <img alt="" class="item-image" :src="require('../assets/icons/' +item.imgUrl)">
        <div class="item-name">
            {{item.name}}
        </div>
        <div class="item-desc">
            {{item.desc}}
            <span v-if="item.bookStats"> {{item.bookStats}} </span>
        </div>
        <div class="item-stats">
            <span v-if="item.bookSummons" style="grid-column: span 3"> {{item.bookSummons}} </span>
            
            <span v-if="item.function==='craft'">Hit points: {{item.HPmod}} </span>
            <span v-if="item.function==='craft'">Initiative: {{item.initiativeMod}} </span>
            <span v-if="item.function==='craft'">Damage: {{item.damageMod}}</span>
            <span v-if="item.function==='craft'" style="grid-column: span 3">Available in: {{item.availability}}</span>
            <span v-if="price">Price: {{price}}</span>
            <span v-if="sellValue">Sell value: {{sellValue}}</span>
            
            
            
        </div>
    </div>
</template>

<script>
export default {
    props: ['item', 'price', 'sellValue']

}
</script>

<style>
    .item { 
        height: 100px;
        
        display: grid;
        grid-template-areas: 
            "icon name name name"
            "icon description description description"
            "icon stats stats stats";
        grid-template-columns: 1fr 3fr;
        padding: .3rem;
        margin: .5rem;
        border: 1px solid #000;
        background: #262424;
        cursor: pointer;
    }

    .item-image {
        grid-area: icon;
        width: 100%;
        max-width: 100%;
        height: 70px;
        align-self: center;
       
    }

    .item-name {
        grid-area: name;
    }

    .item-desc {
        grid-area: description;
        font-size: .9rem;
    }

    .item-stats {
        font-size: .9rem;
        grid-area: stats;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
    }

</style>
