<template>
    <div class="character-sheet">
        <div class="character-wrapper">
            <div class="player-char-name"> <h3>Name:</h3> {{playerCharacter.name}}</div>
            <ul class="statistics-ul">
                <li><h4>Statistics:</h4> </li>
                <li>Endurance: {{playerCharacter.statistics.Endurance}} </li>
                <li>Intelligence: {{playerCharacter.statistics.Intelligence}}</li>
                <li>Luck: {{playerCharacter.statistics.Luck}}</li>
                <li>Magic find: {{playerCharacter.magicFind()}} </li>
                <li>Action points: {{playerCharacter.calculateActionPoints()}}</li>
                
            </ul>
            <div class="skills-display-name"><h4>Skill effects:</h4> 
                <ul class="skill-list-sheet">
                    <li v-for="(skill, index) in playerCharacter.skillsSelected" :key="index">{{skill.effectDesc}}</li>
                </ul>
            </div>
            <p>Gold: {{playerCharacter.gold}}</p>

        </div>

        
        
        

        <div class="buttons-wrapper">
            
            
            <div class="close-btn btn" @click="closeCharacter">CLOSE</div>
            <div class="btn " :class="{'level-up-active': levelUpAvailable}">LEVEL UP</div>
        </div>
    </div>
</template>

<script>
import {gameStore} from '@/stateManagement/gameStore.js';

export default {
    data() {
        return {
            playerCharacter: gameStore.playerCharacter,
        }
    },
    methods: {
        closeCharacter() {
            this.$emit('closeCharacter');
        }
    },
    computed: {
        levelUpAvailable() {
            if (gameStore.playerCharacter.experience >= gameStore.playerCharacter.experienceForNextLevel()) {
                return true;
            } else {
                return false;
            }
        }
    }

}
</script>

<style scoped>
.character-sheet {
    background: #d8cfba;
    color: #000;
    height: 100vh;
    width: 100%;
    position: absolute;
    display: grid;
    
    align-content: center;
    z-index: 2;
    -webkit-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    -moz-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
}

.character-wrapper {
    padding: 1rem;
}

.buttons-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.btn {
    text-align: center;
    margin: 0 .5rem;
    padding: .5rem 1rem ;
    border: 1px solid #000;
}
.level-up-active {
    color: white;
    background: black;
    border: 1px solid black;
}
</style>
