<template>
    
    <div class="workshop">
        <div class="workshop-grid">
            
            <div class="create-section" :class="{'create-section-sm': selectedDemonology}">
                <h2>A NEW CREATION</h2>
                <h3>{{selectedCreationMode}}</h3>
                <div class="head-section">
                    
                    <div class="head-flex double" >
                        <p v-if="!selectedDemonology">Head</p>
                        <p v-if="selectedAutomaton || selectedDemonology">Brain</p>
                        <p v-if="selectedNecromancy || selectedDemonology">Book</p>                        
                        <div class="head-component margin-right" v-if="!selectedDemonology">
                            <div class="head frame " @click="selectMaterials('heads')" :style="{backgroundImage: 'url('+require('../assets/icons/'+selectedHeadDisplay)+')'}" v-if="!selectedDemonology"></div>
                        </div>
                        <div class="brain-component frame empty-frame automation-variant" :class="{'margin-right': selectedDemonology}" v-if="!selectedNecromancy" @click="selectMaterials('brains')" :style="{backgroundImage: 'url('+require('../assets/icons/'+selectedBrainDisplay)+')'}"></div>
                        <div class="necromancy-book-component frame empty-frame" v-if="selectedNecromancy || selectedDemonology" @click="selectMaterials('necroBooks')" :style="{backgroundImage: 'url('+require('../assets/icons/'+selectedNecroBookDisplay)+')'}"></div>
                    </div>
                    
                </div>
                <div class="arms-section" v-if="!selectedDemonology">
                    <p>Arms</p>
                    <div class="arms-component frame" @click="selectMaterials('arms')" :style="{backgroundImage: 'url('+require('../assets/icons/'+selectedArmsDisplay)+')'}"></div>
                </div>

                <div class="torso-section ">
                    
                    <div class="torso-flex double" >
                        <p v-if="selectedAutomaton || selectedNecromancy">Torso</p>
                        <p v-if="selectedAutomaton || selectedDemonology">Heart</p>
                        <p v-if="selectedNecromancy || selectedDemonology">Schematics</p>
                        <div class="torso-component frame " v-if="selectedAutomaton || selectedNecromancy" :class="[{'margin-right': selectedAutomaton}]" :style="{backgroundImage: 'url('+require('../assets/icons/'+selectedTorsoDisplay)+')'}" @click="selectMaterials('torsos')"></div>
                    <div class="heart-component automation-variant frame empty-frame" v-if="selectedAutomaton || selectedDemonology" @click="selectMaterials('hearts')" :style="{backgroundImage: 'url('+require('../assets/icons/'+selectedHeartDisplay)+')'}"></div>
                    <div class="recipe-component frame empty-frame" v-if="selectedNecromancy" @click="selectMaterials('necroRecipes')" :style="{backgroundImage: 'url('+require('../assets/icons/'+selectedNecroRecipeDisplay)+')'}"></div>
                    <div class="recipe-component frame empty-frame" v-if="selectedDemonology" @click="selectMaterials('demoRecipes')" :style="{backgroundImage: 'url('+require('../assets/icons/'+selectedDemoRecipeDisplay)+')'}"></div>
                    
                    </div>
                </div>
                
                <div class="legs-section " v-if="!selectedDemonology">
                    <p>Legs</p>
                    <div class="legs-component  frame" @click="selectMaterials('legs')" :style="{backgroundImage: 'url('+require('../assets/icons/'+selectedLegsDisplay)+')'}"></div>
                </div>
                
                <div class="creatures-display btn" @click="openCreations" >BROWSE YOUR CREATIONS</div>
            </div>
            
            <div class="select-mode-grid">
                <div class="automaton-creation mode-btn" @click="select('automaton')">
                    AUTOMATON
                </div>
                <div class="demonology-creation mode-btn" @click="select('demonology')">
                    DEMONOLOGY
                </div>
                <div class="necromancy-creation mode-btn" @click="select('necromancy')">
                    NECROMANCY
                </div>
            </div>

            <div class="creature-stats">
                <p v-if="selectedCreationMode==='AUTOMATON'">Automatons are not limited by rigid formulas of magic. You can combine body parts unrestrained to create imaginative abominations.</p>
                <p v-if="selectedCreationMode==='NECROMANCY'"> {{necromancyDesc}} </p>
                
                <p v-if="selectedCreationMode==='DEMONOLOGY'">Select the schematics for the demonic entity you want to summon.</p>
                <div class="stats-monster-name" >
                    
                </div>
            </div>

            <div class="buttons-grid">
                <div class="create-btn btn" @click="create">CREATE <span>({{actionPoints}})</span></div>
                <div class="return-btn btn" @click="returnToMain">RETURN</div>
            </div>

            <!-- display materials and select one -->
            <div class="display-materials-modal" v-if="displayMaterials">
                <h2>SELECT AN ITEM</h2>
                
                <h3 v-if="!materialsPresent">No materials to use in this slot</h3>
                <div class="display-items-to-craft-container" v-if="materialsPresent">
                    <item v-for="(item, index) in temporaryItemsToCraft" :key="index" :item="item" @click.native="selectTheItem(item, index)"></item>
                </div>
                <div class="selected-item-container">
                    <h3>Selected item:</h3>
                    <item :item="selectedDisplayedMaterials" v-show="selectedItem"></item>
                </div>
                <div class="close btn" @click="closeDisplayMaterials">CLOSE</div>
            </div>
            
            <!-- info display modal -->
            <transition name="show-modal">
        <div class="info-modal" v-if="displayInfo">
            <p>{{this.message}}</p> 
        </div>
            </transition>
            
        </div>
    </div>

</template>

<script>
import {gameStore} from '@/stateManagement/gameStore.js';
import item from '@/components/itemComponent.vue';
import {setTimeout} from 'timers';



export default {
    props: ['actionPoints'],
    components: {
        item
    },
    data() {
        return {
            displayInfo: false,
            message: '',
            selectedAutomaton: true,
            selectedDemonology: false,
            selectedNecromancy: false,
            displayMaterials: false,
            selectedCreationMode: 'AUTOMATON',
            selectedItem: false,
            selectedDisplayedMaterials: {name: 'Select a body part', imgUrl: 'cog.svg', desc: 'Select a body part to be used in the experiment', type: 'select', value: 1, HPmod: 1, number: 1},
            temporaryItemsToCraft: [],
            
            
            selectedHead: {},
            selectedTorso: {},
            selectedArms: {},
            selectedLegs: {},

            selectedBrain: {},
            selectedHeart: {},

            selectedNecroBook:{},
            selectedNecroRecipe: {},

            selectedDemoRecipe: {},

            creations: []
        }
    },
    methods: {
        select(value) {
            if (value == 'automaton') {
                this.selectedCreationMode = value.toUpperCase();
                this.selectedAutomaton = true;
            this.selectedDemonology = false;
            this.selectedNecromancy = false;
            this.clear();

            } else if (value == 'demonology') {
                this.selectedCreationMode = value.toUpperCase();
                
                this.selectedAutomaton = false;
            this.selectedDemonology = true;
            this.selectedNecromancy = false;
            this.clear();
            } else if (value == 'necromancy') {
                this.selectedCreationMode = value.toUpperCase();
                
                this.selectedAutomaton = false;
            this.selectedDemonology = false;
            this.selectedNecromancy = true;
            this.clear();
            }
            
        },
        clear() {
            
            this.selectedArms = {};
            this.selectedLegs = {};
            this.selectedTorso = {};
            this.selectedHead = {};
            this.selectedBrain = {};
            this.selectedHeart = {};
            this.selectedNecroBook = {};
            this.selectedNecroRecipe = {};
        },
        clearSelectedItems() {
            
            //clear items and delete them from inventory
            if (JSON.stringify(this.selectedArms) !== '{}') {
                for (let i = 0; i < gameStore.playerCharacter.inventory.craftingParts.arms.length; i++) {
                   
                if (this.selectedArms.name === gameStore.playerCharacter.inventory.craftingParts.arms[i].name) {
                    
                    gameStore.playerCharacter.inventory.craftingParts.arms.splice(i, 1);
                    break;
                    }
                }
            }
            if (JSON.stringify(this.selectedLegs) !== '{}') {
                
                for (let i = 0; i < gameStore.playerCharacter.inventory.craftingParts.legs.length; i++) {
                if (this.selectedLegs.name === gameStore.playerCharacter.inventory.craftingParts.legs[i].name) {
                    
                    gameStore.playerCharacter.inventory.craftingParts.legs.splice(i, 1);
                    break;
                    }
                }
            }

           
            if (JSON.stringify(this.selectedHead) !== '{}') {

                for (let i = 0; i < gameStore.playerCharacter.inventory.craftingParts.heads.length; i++) {
                if (this.selectedHead.name === gameStore.playerCharacter.inventory.craftingParts.heads[i].name) {
                    gameStore.playerCharacter.inventory.craftingParts.heads.splice(i, 1); 
                    break;
                    }
                }

            }  if (JSON.stringify(this.selectedTorso) !== '{}') {
                  for (let i = 0; i < gameStore.playerCharacter.inventory.craftingParts.torsos.length; i++) {
                if (this.selectedTorso.name === gameStore.playerCharacter.inventory.craftingParts.torsos[i].name) {
                    
                    gameStore.playerCharacter.inventory.craftingParts.torsos.splice(i, 1);
                   
                    break;
                    }
                }              
                
            }
            if (JSON.stringify(this.selectedHeart) !== '{}') {
                  for (let i = 0; i < gameStore.playerCharacter.inventory.craftingParts.hearts.length; i++) {
                if (this.selectedHeart.name === gameStore.playerCharacter.inventory.craftingParts.hearts[i].name) {
                    
                    gameStore.playerCharacter.inventory.craftingParts.hearts.splice(i, 1);
                   
                    break;
                    }
                }              
            }
            if (JSON.stringify(this.selectedBrain) !== '{}') {
                  for (let i = 0; i < gameStore.playerCharacter.inventory.craftingParts.brains.length; i++) {
                if (this.selectedBrain.name === gameStore.playerCharacter.inventory.craftingParts.brains[i].name) {
                    
                    gameStore.playerCharacter.inventory.craftingParts.brains.splice(i, 1);
                   
                    break;
                    }
                }               
            }
            // NEC BOOK and demo book are not in this list because it always stays in the inventory
            // 
            
            if (JSON.stringify(this.selectedNecroRecipe) !== '{}') {
                  for (let i = 0; i < gameStore.playerCharacter.inventory.necroRecipes.length; i++) {
                if (this.selectedNecroRecipe.name === gameStore.playerCharacter.inventory.necroRecipes[i].name) {
                    gameStore.playerCharacter.inventory.necroRecipes.splice(i, 1);
                    break;
                    }
                }
            }
            this.clear();
        },
        returnToMain() {
            this.clear();
            
            this.$emit('closeWorkshop');
                        
        },
        create() {
            switch(this.selectedCreationMode) {
                case "AUTOMATON": if (this.actionPoints > 0) {
                    if (JSON.stringify(this.selectedArms) === '{}' || JSON.stringify(this.selectedLegs) === '{}' || JSON.stringify(this.selectedHead) === '{}' || JSON.stringify(this.selectedTorso) === '{}' || JSON.stringify(this.selectedBrain) === '{}' || JSON.stringify(this.selectedHeart) === '{}') {
                        this.displayInfoFn("Fill every slot before creating a monster!");
                        
                        break;

                    }
                this.addTheCreation();
                // this.itemDisplay = true;

                
            this.$emit('deductActionPoints');      
            } else {
                this.displayInfoFn("Not enough action points to do that!");
                
                return;
            }
            break;
            case "DEMONOLOGY" :

            break;
            case "NECROMANCY": if (this.actionPoints > 0) {
                    if (JSON.stringify(this.selectedNecroBook) === '{}' || JSON.stringify(this.selectedArms) === '{}' || JSON.stringify(this.selectedLegs) === '{}' || JSON.stringify(this.selectedHead) === '{}' || JSON.stringify(this.selectedTorso) === '{}' || JSON.stringify(this.selectedNecroRecipe) === '{}' ) {
                        this.displayInfoFn("Fill every slot before creating a monster!");
                        break;
                    //check for creating a zombie
                    } else if (this.selectedNecroRecipe.name.includes('Raise Zombie')) {
                        if ( this.selectedNecroBook.name!=='Necromancy Book (1)' || this.selectedLegs.name !== 'Rotting Human Legs'|| this.selectedArms.name !== 'Rotting Human Arms' || this.selectedHead.name !== 'Rotting Human Head' || this.selectedTorso.name !== 'Rotting Human Torso') {
                            this.displayInfoFn("To create a zombie you need exactly Rotting Human Head, Rotting Human Arms, Rotting Human Torso and Rotting Human Legs. Other body parts will not work with the selected spell!");
                            break;
                        }
                    this.addTheCreation();
                    //check for creating a skeleton
                    } else if (this.selectedNecroRecipe.name.includes('Raise Skeleton')) {
                        if (this.selectedNecroBook.name!=='Necromancy Book (1)' || this.selectedLegs.creation !== 'skeleton'|| this.selectedArms.creation !== 'skeleton' || this.selectedHead.creation !== 'skeleton' || this.selectedTorso.creation !== 'skeleton') {
                            this.displayInfoFn("To create a skeleton you need bone elements in all the slots! Other body parts will not work!");
                            break;
                        }
                        this.addTheCreation();
                    } else if (this.selectedNecroRecipe.name.includes('Raise Vampire')) {
                        if (this.selectedNecroBook.name!=='Necromancy Book (3)' || this.selectedLegs.creation !== 'vampire'|| this.selectedArms.creation !== 'vampire' || this.selectedHead.creation !== 'vampire' || this.selectedTorso.creation !== 'vampire') {
                            this.displayInfoFn("To create a Vampire you need vampiric elements in all the slots! Other body parts will not work!");
                            break;
                        }
                        this.addTheCreation();                        
                    }

                
            this.$emit('deductActionPoints');            
            } else {
                this.displayInfoFn("Not enough action points to do that!");
                
                return;
            }
            break;
            }
            
        },
        addTheCreation() {
        // creation formula
        let self = this;
        let getMonsterHP = 0;
        let monsterName = '';
        let monsterImgUrl ='';
        let monsterRegen = 0;
        let monsterDmg = 0;
        let monsterInitiative = 0;
        let monsterAbsorption = 0;
                
        switch(this.selectedCreationMode) {
            case "NECROMANCY": 
            getMonsterHP += this.selectedArms.HPmod + this.selectedLegs.HPmod + this.selectedTorso.HPmod + this.selectedHead.HPmod + gameStore.playerCharacter.necromancyHPbonus + this.selectedNecroRecipe.HPmod;
            monsterRegen += this.selectedArms.regeneration + this.selectedHead.regeneration + this.selectedTorso.regeneration + this.selectedLegs.regeneration;            
            monsterDmg += this.selectedArms.damageMod + this.selectedHead.damageMod + this.selectedTorso.damageMod + this.selectedLegs.damageMod;       
            monsterInitiative += this.selectedArms.initiativeMod + this.selectedHead.initiativeMod + this.selectedTorso.initiativeMod + this.selectedLegs.initiativeMod;
            monsterAbsorption += this.selectedArms.absorption + this.selectedHead.absorption + this.selectedTorso.absorption + this.selectedLegs.absorption;
            
            break;
            case "AUTOMATON":
            getMonsterHP += this.selectedArms.HPmod + this.selectedLegs.HPmod + this.selectedTorso.HPmod + this.selectedHead.HPmod + this.selectedHeart.HPmod + this.selectedBrain.HPmod + gameStore.playerCharacter.automatonHPbonus;
            monsterRegen += this.selectedArms.regeneration + this.selectedHead.regeneration + this.selectedTorso.regeneration + this.selectedLegs.regeneration + this.selectedHeart.regeneration + this.selectedBrain.regeneration;
            monsterDmg += this.selectedArms.damageMod + this.selectedLegs.damageMod + this.selectedTorso.damageMod + this.selectedHead.damageMod + this.selectedHeart.damageMod + this.selectedBrain.damageMod + gameStore.playerCharacter.automatonDamageBonus;
            monsterInitiative += this.selectedArms.initiativeMod + this.selectedLegs.initiativeMod + this.selectedTorso.initiativeMod + this.selectedHead.initiativeMod + this.selectedHeart.initiativeMod + this.selectedBrain.initiativeMod;
            monsterAbsorption += this.selectedArms.absorption + this.selectedLegs.absorption + this.selectedTorso.absorption + this.selectedHead.absorption + this.selectedHeart.absorption + this.selectedBrain.absorption;
            
            break;
            case "DEMONOLOGY":
            getMonsterHP += this.selectedArms.HPmod + this.selectedLegs.HPmod + this.selectedTorso.HPmod + this.selectedHead.HPmod + gameStore.playerCharacter.demonologyHPbonus;
            monsterRegen += this.selectedHeart.regeneration + this.selectedBrain.regeneration;
            monsterDmg += this.selectedHeart.damageMod + this.selectedBrain.damageMod + gameStore.playerCharacter.demonologyDamageBonus;
            monsterDmg += this.selectedHeart.initiativeMod + this.selectedBrain.initiativeMod;
            monsterAbsorption += this.selectedHeart.absorption + this.selectedBrain.absorption;
            
            break;
            }
            
                
            // monster name generation function
            function generateName() {
                if (self.humanoidAutomaton) {
                    monsterName = "Humanoid Automaton";
                    monsterImgUrl = "automaton.svg";                 
                } else if (self.werewolf) {
                    monsterName = "Werewolf";
                    monsterImgUrl = 'werewolf.svg';
                    getMonsterHP += gameStore.playerCharacter.werewolvesHpBonus;
                    
                    
                } else if (self.vampire) {
                    monsterName = "Vampire";
                    monsterImgUrl = 'vampire-girl.svg';
                    getMonsterHP += gameStore.playerCharacter.vampiresHpBonus;
                    
                } else {
                    if (self.selectedHead.name === "Bull's Head") {
                        monsterName = "Minotaur";
                        monsterImgUrl = 'minotaur.svg';
                    } else if (self.selectedNecroRecipe.name === "Raise Skeleton") {
                        monsterName = "Skeleton";
                        monsterImgUrl = 'skeleton.svg';
                    } else if (self.selectedNecroRecipe.name === "Raise Zombie") {
                        monsterName = "Zombie";
                        monsterImgUrl = 'zombie.svg';
                    } else if (self.selectedHead.name.includes('Werewolf') || self.selectedArms.name.includes('Werewolf')) {
                        monsterName = "Wolf-man";
                        monsterImgUrl = "wolf-man.svg";
                    } else {
                        monsterName = "Automaton";
                        monsterImgUrl = "automaton.svg";
                    }
                }
            }
            
            // run stats combination and generate name
            generateName();
            
            //constructor function for a monster
            let Creation = function(name, hp, imgUrl, regeneration, dmg, initiative, absorption) {
                this.imgUrl = imgUrl;
                this.name = name;
                this.hp = hp;
                this.maxHp = hp;
                this.regeneration = regeneration;
                this.dmg = dmg;
                this.initiative = initiative;
                this.owner = "player";
                this.position = 1;
                this.absorption = absorption;
            } 
            let newCreation = new Creation(monsterName, getMonsterHP, monsterImgUrl, monsterRegen, monsterDmg, monsterInitiative, monsterAbsorption);
            this.creations.push(newCreation);
            
            
            gameStore.playerCharacter.creatures.push(...this.creations);
            this.creations = [];
            
            this.clearSelectedItems();
            gameStore.gameState.madeCreatures++;
            this.displayInfoFn("Monster created!");
        },
        selectMaterials(materials){
            
            switch (materials) {
                case "heads":
                
                    if (JSON.stringify(this.selectedHead) !== '{}')
                    {
                        this.selectedDisplayedMaterials = this.selectedHead;
                    }
                    this.pushCraftsToTemporaryItems(materials);
                    break;
                case "arms":
                if (JSON.stringify(this.selectedArms) !== '{}'){
                    this.selectedDisplayedMaterials = this.selectedArms;
                    
                }
                this.pushCraftsToTemporaryItems(materials);
                    break;
                case "legs":
                    if (JSON.stringify(this.selectedLegs) !== '{}')
                    {
                    this.selectedDisplayedMaterials = this.selectedLegs;
                        }
                    this.pushCraftsToTemporaryItems(materials);
                    break;
                case "torsos": 
                    if (JSON.stringify(this.selectedTorso) !== '{}') {
                    this.selectedDisplayedMaterials = this.selectedTorso;
                        
                    }
                    this.pushCraftsToTemporaryItems(materials);
                    break;
                case "brains":
                    if (JSON.stringify(this.selectedBrain) !== '{}') {
                    this.selectedDisplayedMaterials = this.selectedBrain;
                    }
                    this.pushCraftsToTemporaryItems(materials);
                    break;
                case "hearts":
                    if (JSON.stringify(this.selectedHeart) !== '{}') {
                    this.selectedDisplayedMaterials = this.selectedHeart;
                    }
                    this.pushCraftsToTemporaryItems(materials);
                    break;
                case "necroBooks":
                   
                        
                    this.temporaryItemsToCraft.push(...gameStore.playerCharacter.inventory.necroBooks);
                    break;
                    case "necroRecipes":
                    
                        
                    this.temporaryItemsToCraft.push(...gameStore.playerCharacter.inventory.necroRecipes);
                   
                    break;
            }
            
            
            this.displayMaterials=true;
            
            
        },
        pushCraftsToTemporaryItems(materials) {
            let temporaryItems=[];
            temporaryItems.push(...gameStore.playerCharacter.inventory.craftingParts[materials]);
            temporaryItems.forEach( (item) => {
                if (this.selectedCreationMode === "DEMONOLOGY" && item.availability.includes('demonology')) {
                    this.temporaryItemsToCraft.push(item);
                } else if (this.selectedCreationMode === "NECROMANCY" && item.availability.includes('necromancy')) {
                    this.temporaryItemsToCraft.push(item);
                } else if ( this.selectedCreationMode === "AUTOMATON" && item.availability.includes('automaton') ) {
                    this.temporaryItemsToCraft.push(item);
                }
            })
            
            
            
        },
        closeDisplayMaterials() {
            this.temporaryItemsToCraft = [];
            this.selectedDisplayedMaterials = {name: 'Select a body part', imgUrl: 'cog.svg', desc: 'Select a body part to be used in the experiment', type: 'select', value: 1, HPmod: 1, number: 1};
            
            this.displayMaterials=false;
            
            
        },
        selectTheItem(item) {
            
            switch (item.type) {
                case "heads":
                    
                    this.selectedHead = item;
                    
                    break;

                case "arms":
                   
                    this.selectedArms = item;
                    break;

                case "legs":
                    
                    this.selectedLegs = item;
                    break;

                case "torsos": 
                    
                    this.selectedTorso = item;
                    break;

                case "brains":
                    
                    this.selectedBrain = item;
                    break;

                case "hearts":
                    
                    this.selectedHeart = item;
                    break;
                    
                case "necroBooks":
                    
                    this.selectedNecroBook = item;
                    break;

                    case "necroRecipes":
                    
                    this.selectedNecroRecipe = item;
                    break;
            }
            this.selectedDisplayedMaterials = item;
            this.selectedItem = true;
        },
       
        openCreations() {
            this.$emit('openCreations')
        },
        displayInfoFn(msg) {
            this.displayInfo = true;
            this.message = msg;
            setTimeout ( () => {
                this.displayInfo = false;
            }, 1000)
        }
    },
    computed: {
        
        selectedHeadDisplay() {
            if (this.selectedHead.imgUrl)
            {
                return this.selectedHead.imgUrl;
            } else {
                return 'cog.svg';
            }
            
        }, 
        selectedTorsoDisplay() {
            if (this.selectedTorso.imgUrl)
            {
                return this.selectedTorso.imgUrl;
            } else {
                return 'cog.svg';
            }
            
        },
        selectedLegsDisplay() {
            if (this.selectedLegs.imgUrl)
            {
                return this.selectedLegs.imgUrl;
            } else {
                return 'cog.svg';
            }
            
        },
        selectedArmsDisplay() {
            if (this.selectedArms.imgUrl)
            {
                return this.selectedArms.imgUrl;
            } else {
                return 'cog.svg';
            }
            
        },
        selectedHeartDisplay() {
            if (this.selectedHeart.imgUrl)
            {
                return this.selectedHeart.imgUrl;
            } else {
                return 'cog.svg';
            }
            
        },
        selectedBrainDisplay() {
            if (this.selectedBrain.imgUrl)
            {
                return this.selectedBrain.imgUrl;
            } else {
                return 'cog.svg';
            }
            
        },
        selectedNecroBookDisplay() {
            if (this.selectedNecroBook.imgUrl) {
                return this.selectedNecroBook.imgUrl
            } else {
                return 'cog.svg'
            }
        },
        selectedNecroRecipeDisplay() {
            if (this.selectedNecroRecipe.imgUrl) {
                return this.selectedNecroRecipe.imgUrl
            } else {
                return 'cog.svg'
            }
        },
        selectedDemoRecipeDisplay() {
            if (this.selectedDemoRecipe.imgUrl) {
                return this.selectedDemoRecipe.imgUrl
            } else {
                return 'cog.svg'
            }
        },
        materialsPresent() {
            if (JSON.stringify(this.temporaryItemsToCraft) === '[]'){
                return false;
            } else {
                return true;
            }
            
        },
        necromancyDesc() {
            if (JSON.stringify(this.selectedNecroRecipe) === '{}') {
                return "Select the schematics for the necromantic entity you want to raise. ";
            } else if (this.selectedNecroRecipe.creation) {
                return this.selectedNecroRecipe.creation;
            } else {
                return "Select the schematics for the necromantic entity you want to raise. ";
                
            }
        },
        humanoidAutomaton() {
            if (this.selectedHead.name.includes('Human') && this.selectedArms.name.includes('Human') && this.selectedTorso.name.includes('Human') && this.selectedLegs.name.includes('Human') && JSON.stringify(this.selectedNecroRecipe) ==="{}"){
                return true;
            } else {
                return false;
            }
        },
        werewolf() {
            if (this.selectedHead.name.includes('Werewolf') && this.selectedArms.name.includes('Werewolf')) {
                return true;
            } else {
                return false;
            }
        },
        vampire() {
            if (this.selectedHead.creation==='vampire' && this.selectedArms.creation==='vampire' && this.selectedTorso.creation==='vampire' && this.selectedLegs.creation==='vampire') {
                return true;
            } else {
                return false;
            }
        }
    }
}
</script>

