<template>
    
    <div class="main-screen">
        
        <div class="top-panel">
            <div class="event-log">
                <!-- <div class="event-log-title"> </div> -->
                <ul class="event-log-info-list scroll">
                    <li v-for="(entry, index) in updateEventLog" :key="index" >{{entry}}</li>
                </ul>
            </div>
            <div class="hostility-progression-bar">
                <div class="hostility-bar-display" :style="{width: hostilityLevel + '%'}"></div>
            </div>
            <!-- Navigation -->
            <div class="nav-btn">
                
                
                <i class="fas fa-cog" @click="openMenu" :class="{'turn-cog': menuOpened}"></i>
            </div>
            <div class="menu-overlay" :class="{'menu-visible': menuOpened}">

                <transition name="load" mode="out-in">
                    
                    <div class="menu-wrapper" v-if="!loadGameOpened" key="menu-list">
                        <ul class="menu-list">
                            <li>SAVE</li>
                            <li @click="loadGameOpened=true">LOAD</li>
                            <li @click="abortAndExitToMenu">EXIT TO MAIN MENU</li>
                        </ul>
                    </div>
                    <div class="load-game-overlay" v-if="loadGameOpened" key="load-list">
                        
                            <ul class="load-game-list menu-list">
                                <h2>LOAD A SAVED GAME</h2>
                                <li class="autosave-load" @click="loadAutosave">AUTOSAVE</li>
                                <li @click="loadGameOpened=false">CANCEL</li>
                            </ul>
                       
                    </div>
                </transition>
            </div>
            

        </div>
        <!-- end of navigation -->

        <!-- MAIN SCREEN -->
        <!-- MAIN SCREEN - locations -->
        <div class="locations-panel">
            <i class="far fa-eye" @click="mapMode=!mapMode" v-if="mobileSize" :class="{'eye-black': mapMode}"></i>
            <transition name="fade" mode="out-in">
            <ul class="locations" v-if="!mapMode" key="lista">
                <li class="btn" @click="openGraveyard">Graveyard</li>
                <li class="btn" @click="townOpened=true">Town</li>
                <li class="btn" @click="openWorkshop">Workshop</li>
                <li class="btn" @click="openCreations">Creations</li>
                <li class="btn">Diplomacy</li>
            </ul>
            <div class="map-view" v-if="mapMode" key="mapa">
                <div class="map-grid">
                    <div class="map-graveyard">
                        <div class="graveyard-map-item" @click="openGraveyard">
                            <img alt="" class="place-image" :src="require('../assets/icons/graveyard.svg')">
                        <p class="map-place-title">Graveyard</p>
                        </div>
                        
                        
                    </div>
                    <div class="map-town">
                        <div class="town-map-item" @click="townOpened=true">
                            <img alt="" class="place-image" :src="require('../assets/icons/town.svg')">
                            <p class="map-place-title">Town</p>
                        </div>
                        
                    </div>
                    <div class="map-workshop">
                        <div class="workshop-map-item" @click="openWorkshop">
                            <img alt="" class="place-image" :src="require('../assets/icons/workshop.svg')">                        
                            <p class="map-place-title">Workshop</p>
                        </div>
                        
                    </div>
                    
                   
                </div>
                <div class="graveyard-grid-btns-container">
                         <li class="btn" @click="openCreations">Creations</li>
                <li class="btn">Diplomacy</li>
                    </div>
            </div>
            </transition>
        </div>

        <!-- MAIN SCREEN - bottom info panel -->
        
        <div class="turn-info-panel">
            <div class="turn-info-container">
                <div class="turn-info-buttons">
                    <ul class="info-buttons">
                        <li @click="characterOpened=true">Character</li>
                        <li>Bestiary</li>
                        <li @click="openInventory">Inventory</li>
                    </ul>
                </div>
                <div class="actions-left-display">
                    <div class="remaining-actions">Remaining action points:</div>
                    <div class="number-of-actions-left">{{currentActionPoints}}</div>
                </div>
                <div class="next-turn-container">
                    <div class="next-turn-wrapper">
                        <div class="next-turn-header">Next turn</div>
                        <div class="next-turn-button" @click="nextTurn"> 
                            <img src="../assets/icons/hourglass.svg" class="next-turn-ico" alt="" :class="{'turn-hourglass': animateHourglass}">
                        </div>
                        <div class="turn-number">Turn: {{turnCounter}}</div>
                    </div>
                </div>
        </div>
        </div>
        <!-- end of MAIN SCREEN -->

        <!-- Graveyard -->
        <graveyard v-if="graveyardOpened" @deductActionPoints="deductAP" @closeGraveyard="closeGraveyard" @corpsesDugForTheTurn="increaseHostility" :actionPoints="currentActionPoints"></graveyard>
        <!-- workshop -->
        <workshop v-if="workshopOneped" @closeWorkshop="workshopOneped=false" @deductActionPoints="deductAP" @openCreations="creationsOpened=true" :actionPoints="currentActionPoints"></workshop>
        <!-- inventory -->
        <inventory v-if="inventoryOpened" @closeInventory="inventoryOpened=false"></inventory>
        <!-- creations browser -->
        <creations v-if="creationsOpened" @closeCreations="creationsOpened=false"></creations>
        <!-- town -->
        <town v-if="townOpened" @closeShop="townOpened=false"></town>
        <!-- character sheet -->
        <character v-if="characterOpened" @closeCharacter="characterOpened=false"></character>
        <!-- battle component -->
        <battle-component v-if="playerAttacked" @playerWins="playerVictory" @gameOver="gameOver"></battle-component>

        <!-- info display modal -->
            
        <div class="info-modal end-game-modal" v-if="endGame">
            <h2>YOU DIED</h2>
            <h4>People from the town defeated your monsters!</h4>
            <p>{{endGameMessage}}</p>
            <ul class="end-screen-table">
                <h3>Records:</h3>
                <li>Battles won: {{playersWins}}</li>
                <li>Total creatures made: {{madeCreaturesTotal}}</li>
                <li>Turns survived: {{turnCounter}}</li>
            </ul>
            <div class="btn" @click="abortAndExitToMenu">CONTINUE</div>
        </div>
            
    </div>

</template>

<script>
import {gameStore} from '@/stateManagement/gameStore.js';
import {unitsOfOrder} from '@/stateManagement/gameStore.js';
import Graveyard from '@/components/graveyard.vue';
import Workshop from '@/components/workshop.vue';
import Inventory from '@/components/inventory.vue';
import Character from '@/components/characterSheet.vue';
import Creations from '@/components/creations.vue';
import {setTimeout} from 'timers';
import Town from '@/components/townComponent.vue';
import BattleComponent from '@/components/battleComponent.vue';



export default {
    components: {
            Graveyard,
            Workshop,
            Inventory,
            Creations,
            Town,
            Character,
            BattleComponent
        },
    data() {
    return {
            menuOpened: false,
            graveyardOpened: false,
            townOpened: false,
            workshopOneped: false,
            animateHourglass: false,
            inventoryOpened: false,
            creationsOpened: false,
            characterOpened: false,
            loadGameOpened: false,
            turnCounter: gameStore.gameState.turnCounter,
            currentActionPoints: gameStore.playerCharacter.calculateActionPoints(),
            infoForEventLog: [],
            playerAttacked: false,
            endGame: false,
            endGameMessage: "For your atrocities, for desacrating the graves of their friends and loved ones, for creating abominations, summoning unholy demons and dark entities from beyond the veil, for all of this, you hang tonight. At least, that's what they say to you, as they put the noose around your neck.",
            unitsOfOrder: [],
            playersWins: 0,
            dugGraves: false,
            mapMode: false
            
            

        }
    },
    methods: {
        openMenu() {
            this.menuOpened = !this.menuOpened;
            this.loadGameOpened = false;
        },
        openGraveyard() {
            this.graveyardOpened = true;
        },
        openWorkshop() {
            this.workshopOneped = true;
        },
        openInventory() {
            this.inventoryOpened = true;
        },
        openCreations() {
            this.creationsOpened = true;
        },
        playerVictory() {
            this.playerAttacked = false;
            this.playersWins++;
            
        },
        loadAutosave() {
            if (localStorage.length === 0) {
                return;
            }
            gameStore.gameState = JSON.parse(localStorage.getItem('autosaveGameState'));
            gameStore.playerCharacter = JSON.parse(localStorage.getItem('autosavePlayerCharacter'));
            this.turnCounter = gameStore.gameState.turnCounter;
            this.playersWins = +localStorage.getItem('playersWins');
            
            
            gameStore.playerCharacter.magicFind = function magicFind() {
                return this.statistics.Luck * 2 + this.MFbonus}

            gameStore.playerCharacter.calculateActionPoints= function calculateActionPoints() {
                let result = Math.floor(this.statistics.Endurance/3) ;
                if (result > 1) {
                    return result + this.ManOfAction;
                } else {
                    return 1 + this.ManOfAction;
                }
            }

            gameStore.playerCharacter.experienceForNextLevel = function experienceForNextLevel() { 
                return this.level * 500}
            
            
            
            this.playerAttacked = false;
            this.unitsOfOrder = [];
            this.menuOpened = false;
            this.inventoryOpened = false;
            this.creationsOpened = false;
            this.workshopOneped = false;
            this.graveyardOpened = false;
            this.infoForEventLog.push("Autosave loaded.");
            this.updateScroll();
                
        },
        nextTurn() {
            // make autosave
            // TODO make the game state remember used action points because they get renewed when the game is loaded
            // and if the player used them and loaded the game he gets one set more
            localStorage.setItem('autosavePlayerCharacter', JSON.stringify(gameStore.playerCharacter));
            localStorage.setItem('autosaveGameState', JSON.stringify(gameStore.gameState));
            localStorage.setItem('playersWins', this.playersWins);
            console.log(this.mobileSize)
            
            
            this.generateOrderUnits();
            this.animateHourglass = true;
            
            this.currentActionPoints = gameStore.playerCharacter.calculateActionPoints();
            this.turnCounter++;
            gameStore.gameState.turnCounter = this.turnCounter;

            
            this.infoForEventLog.push("Begins turn " + this.turnCounter+".");
            this.infoForEventLog.push("Game saved on autosave.");
            
            this.pushInfoForEventLog();
            this.infoForEventLog = [];
            //increase hostility when player dug graves this turn
            if (this.dugGraves) {
                gameStore.playerCharacter.hostilityLevel +=12;
                
            }
            this.dugGraves = false;
            
            setTimeout(() => {
                this.animateHourglass = false;
                this.updateScroll();
                } , 400)
            
            // helper function to roll random number
            let chanceToIncreaseHostility = this.randomIntNumberFromInterval(1, 100);
            if (chanceToIncreaseHostility > 60) {
                gameStore.playerCharacter.hostilityLevel +=2;
                this.infoForEventLog.push("People from the town start to be wary of your presence...")
            }

            this.checkForHostility();
                
        },
        increaseHostility() {
            if (this.dugGraves === false) {
            this.infoForEventLog.push('You desacrated the nerbay graveyard. Town\'s hostility level increases');                
            }
            this.dugGraves = true;
        },
        randomIntNumberFromInterval(min, max) {return Math.floor(Math.random() * (max - min + 1) + min);},
        checkForHostility() {
            if (this.hostilityLevel >= 100) {
                this.playerAttacked = true;
                gameStore.playerCharacter.hostilityLevel = 0;
            }
        },
        pushInfoForEventLog() {
            gameStore.eventLog.push(...this.infoForEventLog);
        },
        closeGraveyard() {
            this.graveyardOpened = false;
            
            
        },
        updateScroll() {
            let scroll = this.$el.querySelector('.scroll');
            scroll.scrollTop = scroll.scrollHeight;
        },
        deductAP() {
            this.currentActionPoints--;
        },
        abortAndExitToMenu() {
            gameStore.gameState.isNewGame = false;
            gameStore.gameState.characterCreated = false;
            gameStore.playerCharacter = {};
            gameStore.eventLog = [];
            gameStore.gameState.turnCounter = 1;
        },
        gameOver() {
            this.playerAttacked = "false";
            this.endGame = true;
            this.endGameMessage = "For your atrocities, for desacrating the graves of their friends and loved ones, for creating abominations, summoning unholy demons and dark entities from beyond the veil, for all of this, you hang tonight. At least, that's what they say to you, as they put the noose around your neck.";
        },
        generateOrderUnits() {
            // set a units value from player units, maybe also gather info from previous fights, how many times and vs what did player win, and adjust
            //difficulty accordingly
            //helper function to pick a unit
                    function rollForUnitOfOrder(value) {
                        if (value === 1) {
                            return 'peasant';
                        } else if (value > 1 && value < 11) {
                            return 'blacksmith';
                        } else if (value > 11 && value < 20 ) {
                            return 'pikeman'
                        } else if (value > 20 && value < 30) {
                            return 'executioner';
                        } else if (value > 30 && value < 50) {
                            return 'assassin';
                        } else if (value > 50 && value < 100) {
                            return 'knight'
                        } else if (value > 100) {
                            //angel here when ready
                            return 'knight';
                        }
                        //add a crusader or sth
                    }
            if (JSON.stringify(this.unitsOfOrder) ==="[]" ) {
                // determine the number of units that will spawn for the AI
                //TO DO more advanced function that considers unit strength etc
                
                
                let numberOfUnits = 2 + this.playersWins;
                
                if (numberOfUnits > 8) {
                    numberOfUnits = 8
                }
                
                // loop through units in game store and add them to the stack
                for (let i = 0; i < numberOfUnits; i++) {
                    let randomUnitQualityRoll = this.randomIntNumberFromInterval(1, this.turnCounter);
                    //determine the quality of the unit (maybe also considering their number, in order to have some weaker and some stronger ones)
                    let determineUnitQuality = Math.floor(this.playersWins/2)+Math.floor(this.turnCounter/2) + randomUnitQualityRoll;
                    //peasant 1, blacksmith 5, pikeman 10, executioner, assassin knight 60
                    let unit = undefined;
                    let rollInUnitRange = this.randomIntNumberFromInterval(1, determineUnitQuality);
                    
                    switch (rollForUnitOfOrder(rollInUnitRange)) {
                        default: 
                        unit = unitsOfOrder.createPeasant();
                        break;
                        case 'peasant':
                        unit = unitsOfOrder.createPeasant();
                        break;
                        case 'blacksmith':
                        unit = unitsOfOrder.createBlacksmith();
                        break;
                        case 'pikeman':
                        unit = unitsOfOrder.createPikeman();
                        break;
                        case 'executioner':
                        unit = unitsOfOrder.createExecutioner();
                        break;
                        case 'assassin':
                        unit = unitsOfOrder.createAssassin();
                        break;
                        case 'knight':
                        unit = unitsOfOrder.createKnight();
                        break;
                    }
                    
                    this.unitsOfOrder.push(unit);
                    
                }
                gameStore.gameState.enemyUnits = this.unitsOfOrder;
            } else {
                return;
            }
        }
    },
    computed: {
        updateEventLog() {
            return gameStore.eventLog;
        },
        hostilityLevel(){
            return gameStore.playerCharacter.hostilityLevel;
        },
        madeCreaturesTotal() {
            return gameStore.gameState.madeCreatures;
        },
        mobileSize() {
            if (window.innerHeight > 639) {
                return true;
            } else {
                return false;
            }
        } 
    }
    

}
</script>

<style scoped>



.main-screen {
    height: 100vh;
    display: grid;
    grid-template-rows: 1fr 6fr 2fr;
    position: relative;
    background: #1c1c1c;
    color: #fff;
}

/* menu section */

.top-panel {
    position: relative;

}

.nav-btn {
    position: absolute;
    top: 0;
    right: 0;
    padding: 1rem;
    cursor: pointer;
    z-index: 5;
    background: transparent;
    
    
    
}

.fa-cog {
    transition: transform .4s ease-in-out;
    
}

.turn-cog {    
    transform: rotate(180deg);
}

.menu-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    transform: translate(100%);    
    transition: all .4s ease-in-out;
    display: grid;
    align-content: center;
    z-index: 4;
    background: #1c1c1c;
}


.menu-visible {
    transform: translate(0);
    }

.menu-wrapper {
    display: grid;
    justify-content: center;
    
    
}

.menu-list  {
    align-self: center;
    text-align: center;
}

.menu-list li {
    margin: 1rem;
    padding: .5rem 1rem;
    background-color: #262424;
    border: #000 1px solid;
}

/* event log */

.event-log {
    background: #d8cfba;
    color: #000;
    height: 110px;
    padding: 1rem;
    display: grid;
    -webkit-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    -moz-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
}

.event-log-title {
    font-size: 1.2rem;
    line-height: 1.4rem;
}

.event-log-info-list {
    overflow-y: auto;
}

/* progression bar styles*/

.hostility-progression-bar {
    height: 10px;
    width: 100%;
    border: 1px solid silver;
}

.hostility-bar-display {
    background: silver;
    height: 10px;
}

/* location panel */

.locations-panel {
    align-self: center;
    display: grid;
    justify-content: center;
    align-content: center;
    text-align: center;
    height: 100%;
    position: relative;
}

.locations {
    line-height: 2rem;
    font-size: 1.5rem;
}

.fa-eye {
    position: absolute;
    top: 2%;
    left: 2%;
    cursor: pointer;
}


/* map view panel */



.map-grid {
    background: url('../assets/icons/map.svg') no-repeat;
    background-size: cover;
    background-position: center center;
    display: grid;
    padding: .5rem;
    grid-template: 
                "graveyard graveyard"
                "town town"
                "workshop workshop"
            
}
.map-graveyard {
    grid-area: graveyard;
    
}

.graveyard-map-item {
    margin-left: 35%;
}

.map-town {
    grid-area: town;
    
}

.town-map-item {
    margin-right: 45%;
}

.map-workshop {
    grid-area: workshop;
    
}

.workshop-map-item {
    margin-left: 40%;
}

.graveyard-grid-btns-container {
    display: flex;  
}

.place-image {
    height: 100px;
    width: auto;
    max-width: auto;

}
@media(max-width: 380px) {

    .place-image {
        height: 80px;
       
    }
    .map-grid {
        padding: 0;
    }
    
}

@media(max-height: 640px) {
    .eye-black {
    color: black;
}


}

.map-place-title {
    margin: 0;
    color: black;

}



/* turn info panel */
.turn-info-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    height: 100%;
    
}

.turn-info-buttons {
    display: grid;
    justify-content: center;
    align-content: center;
    text-align: center;
    
}

.info-buttons li {
    padding: .4rem;
    margin: .4rem 0;
    background-color: #262424;
    border: #000 1px solid;
    font-size: 1.2rem;
    cursor: pointer;
}

.actions-left-display {
    justify-content: center;
    display: flex;
    flex-direction: column;
}

.remaining-actions {
    font-size: 1.4rem;
}

.number-of-actions-left {
    font-size: 3rem;
    text-align: center;
}

.next-turn-container {
    display: grid;
    justify-content: center;
    text-align: center;
    align-content: center;
    border: 1px #000 solid;
    padding: .5rem;
    margin: .5rem;
    background: #262424;
}

.next-turn-button {
    cursor: pointer;
}

.turn-hourglass {
    background: transparent;
    animation: nextTurn .4s forwards;
}

.info-modal {
    display: grid;
    position: absolute;
    top: 0;
    width: 100%;
    justify-content: center;
    align-content: center;
    height: 100vh;
    z-index: 2;
    background: #1c1c1c;
    padding: .3rem;
     
    }

    .info-modal h2, h4 {
        text-align: center;
    }

    .info-modal h2, p {
        margin: .5rem;
        
    }

    .info-modal p {
        padding: 1rem;
        background: #d8cfba;
        color: black;        
        -webkit-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
        -moz-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
        box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);

    }
.btn {
    margin: .4rem;
    padding: .5rem 3rem;
    background-color: #262424;
    border: #000 1px solid;
    text-align: center;
}

/* load game transition */
.load-enter {
    opacity: 0;
    transform: translateZ(-100%);
    transition: all .4s ease-out;
}
.load-enter-active {
      transition: all .4s ease-out;

}

.load-enter-to {
  opacity: 1;
  transform: translateZ(0);
    transition: all .4s ease-out;
  
}

.load-leave-active {
  opacity: 0;
  transform: translateZ(-100%);
  transition: all .4s ease-out;
  
}

/* fade transition */
.fade-enter {
    opacity: 0;
}
.fade-enter-to {
    opacity: 1;
    transition: all .4s ease-in-out;
    
}
.fade-leave-to {
    opacity: 0;
    transition: all .4s ease-in-out;
    
}

/* keyframes */

@keyframes nextTurn {
    from {
        transform: rotate(0);
    } to {
        transform: rotate(360deg);
    }
}

</style>
