<template>
  <div class="menu-screen">
    <div class="title">
      <h1 class="title-header">MAD DOCTOR</h1>
      <div class="big-cog-one cogs">
          <i class="fas fa-cog fa-3x "></i>
      </div>
      <div class="small-cog-two cogs">
            <i class="fas fa-cog fa-2x "></i>  
      </div>
    </div>
    <ul class="menu-options" v-if="!loadOpened">
      <li class="option btn" @click="newGame">NEW GAME</li>
      <li class="option btn" @click='loadOpened = true'>LOAD </li>
      <li class="option btn" @click="displayInfo=true">INFO</li>
    </ul>
    <ul class="saved-games menu-options" v-if="loadOpened">
      <h2>LOAD A SAVED GAME</h2>
      <li class="autosave-load btn" @click="loadAutosave">AUTOSAVE</li>
      <li class="btn" @click="loadOpened=false">CANCEL</li>
    </ul>

    <!-- info section -->

    <div class="info-modal" v-if="displayInfo">
      <div class="info-modal-content-wrapper">
         <h1>HOW TO PLAY</h1>
              <h3>Welcome to Mad Doctor!</h3>
              <p>This is a crafting game, in which you play as a mad scientist trying to create monstrous atrocities from various body parts. Every turn you start with a limited amount of <span class="highlighted">Action Points</span>, which are used for actions like digging for body parts and bringing your creations to life. End the turn to reset your <span class="highlighted">Action Points</span> when they are depleted.</p>
              <p>Beware however, as not everyone is happy with you digging up corpses of their friends and loved ones. Citizens of the nerbay town get more restless the more body parts you dig up. If <span class="highlighted">Hostility Meter</span> fills up, they organise a raiding party to tear down your laboratory. The only thing that can defend you is the monsters you have created, so better create as much of them as you can!</p>
             
      </div>
            <div class="confirmation-btns" >
                <div class="btn" @click="displayInfo=false">CLOSE</div>                
            </div>
    </div>
    
  </div>
</template>

<script>


// @ is an alias to /src

import {gameStore} from '@/stateManagement/gameStore.js';

export default {
  data() {
    return {
      displayInfo: false,
      loadOpened: false,
    }
  },
  methods: {
    newGame() {
      
      gameStore.gameState.isNewGame = true;
      
    },
      loadAutosave() {
            if (localStorage.length === 0) {
              return; }
            gameStore.gameState = JSON.parse(localStorage.getItem('autosaveGameState'));
            gameStore.playerCharacter = JSON.parse(localStorage.getItem('autosavePlayerCharacter'));
            this.turnCounter = gameStore.gameState.turnCounter;
            this.playersWins = +localStorage.getItem('playersWins');

            gameStore.playerCharacter.magicFind = function magicFind() {
                return this.statistics.Luck * 2 + this.MFbonus}

            gameStore.playerCharacter.calculateActionPoints= function calculateActionPoints() {
                let result = Math.floor(this.statistics.Endurance/3) ;
                if (result > 1) {
                    return result + this.ManOfAction;
                } else {
                    return 1 + this.ManOfAction;
                }
            }

            gameStore.playerCharacter.experienceForNextLevel = function experienceForNextLevel() { 
                return this.level * 500}
            
            
            
            this.playerAttacked = false;
            this.unitsOfOrder = [];
            this.menuOpened = false;
            this.inventoryOpened = false;
            this.creationsOpened = false;
            this.workshopOneped = false;
            this.graveyardOpened = false;
            

   
        },
    }
    
  }
  

</script>

