<template>
    <div class="creations">
        <h2>CREATIONS</h2>
        <p>Number of units: {{monstersList.length}}</p>
        <div class="creations-grid">
            <h3 v-if="noMonsters">You haven't created any monsters yet</h3>
            <div class="creatures-wrapper-grid" v-if="!noMonsters">

                <!-- creature card for display -->
                <div class="creature-display" v-for="(monster, index) in monstersList" :key="index" >
                    <div class="creature-portrait">
                        <img :src="require('../assets/icons/'+ monster.imgUrl)" alt="">
                    </div>
                    <div class="creature-stats">
                        <div class="creature-name">
                            
                            {{monster.name}}
                        </div>
                        <div class="creature-hp">

                            Hit points: {{monster.hp}}
                        </div>
                        <div class="creature-dmg">
                            Damage: {{monster.dmg}}
                        </div>
                        <div class="creature-initiative">
                            Initiative: {{monster.initiative}}
                        </div>
                        <div class="special-abilities">
                            <h3>Special abilities:</h3>
                            <ul class="special-abilities-list-sheet">
                                <li v-if="monster.regeneration > 0">Regeneration: {{monster.regeneration}}</li>
                                <li v-if="monster.absorption > 0">Absorbtion: {{monster.absorption}}</li>
                            </ul>
                        </div>
                        <div class="position-display">
                            <h3>Position: {{monster.position}}</h3>
                            
                        </div>
                    </div>
                    <!-- button to assign position -->
                    <div class="assign-unit-position btn" @click="setPosition(monster)">
                        POSITION
                    </div>
                 </div>
                 <!-- end of the creature card -->

            </div>
           
        </div>
        <div class="buttons-wrapper">
            <div class="prepare-defenses-btn btn" @click="openBattle"> DEBUG</div>
            
            <div class="close-btn btn" @click="closeCreations">CLOSE</div>
        </div>
        
        <!-- battle component -->
        <debug-component v-if="debugOpen" @closeBattleScreen="debugOpen=false"></debug-component>
        <!-- assign position component -->
        <div class="assign-position" v-if="assignPositionOpened">
            <h2>ASSIGN POSITION</h2>
            <p>Position you assign to this unit will determine this unit's place on the battlefield. Unassigned positions are assigned automatically before the battle, but the results may be undesireable. </p>
            <p>Unit: <span class="highlighted">{{currentUnit.name}}</span> </p>
            <ul class="position-options">
                <li class="btn" @click="assignPosition(1)">Position 1</li>
                <li class="btn" @click="assignPosition(2)">Position 2</li>
                <li class="btn" @click="assignPosition(3)">Position 3</li>
                <li class="btn" @click="assignPosition(4)">Position 4</li>
                <li class="btn" @click="assignPosition(5)">Position 5</li>
                <li class="btn" @click="assignPosition(6)">Position 6</li>
                <li class="btn" @click="assignPosition(7)">Position 7</li>
                <li class="btn" @click="assignPosition(8)">Position 8</li>
                <li class="btn close-assign" @click="assignPosition(0)">CLOSE</li>
            </ul>
        </div>
    </div>
</template>

<script>
import {gameStore} from '@/stateManagement/gameStore.js';
import debugComponent from '@/components/debugComponent.vue';
// TO DO populate the grid with units on mounted and pass it into battle component, change the setup here if player wants to

export default {
    components: {
        debugComponent
    },
    data() {
        return {
            debugOpen: false,
            assignPositionOpened: false,
            currentUnit:{},
        }
    },
    methods: {
        closeCreations() {
            this.$emit('closeCreations');
        },
        openBattle() {
            this.debugOpen = true;
        },
        setPosition(monster) {
            this.currentUnit = monster;
            this.assignPositionOpened = true;
        },
        assignPosition(num) {
            
            if (num === 0) {
                this.assignPositionOpened = false;
                this.currentUnit = {};
                return;
            }
            this.currentUnit.position = num;
            
            gameStore.playerCharacter.creatures.forEach( (unit) => {
                if (unit.position === this.currentUnit.position && unit !== this.currentUnit) {
                    
                    unit.position =1;
                }
            })
            
                this.assignPositionOpened = false;
                this.currentUnit = {};                
        }
    },
    computed: {
        monstersList() {
            return gameStore.playerCharacter.creatures;
        },
        noMonsters() {
            if (JSON.stringify(gameStore.playerCharacter.creatures) === '[]')
            {return true} else {
                return false
            }
        }
    },

    }
</script>

<style scoped>
.creations {
    background: #1c1c1c;
    color: #fff;
    height: 100vh;
    width: 100%;
    position: absolute;
    display: grid;
    justify-content: center;
    align-content: center;
    z-index: 3;
    }

    .creations h2 {
        text-align: center;
    }

.creatures-wrapper-grid {
        display: grid;
        grid-template-columns: 1fr;
        margin: 1rem;
        grid-gap: 1rem;
    }

    .creations-grid {
        overflow-y: auto;
    }
    .creations-grid h3 {
        text-align: center;
    }

    /* monster card */

/* .creature-display {
    
} */

.creature-portrait img {
    width: 100%;
    max-width: 100%;
    height: auto;
    
}

.creature-stats {
    background: #d8cfba;
    color: #000;
    -webkit-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    -moz-box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
    box-shadow: inset 3px 1px 17px 13px rgba(0,0,0,0.75);
}

/*assign position styles */
.assign-position {
    display: grid;
    position: absolute;
    top: 0;
    width: 100%;
    justify-content: center;
    align-content: center;
    height: 100vh;
    z-index: 2;
    background: #1c1c1c;
    padding: .3rem;
}
.assign-position h2, p {
    margin-bottom: .5rem;
}

.assign-position p {
    line-height: 1.5rem;
    font-size: 1.1rem;
    padding: .5rem;
}

.position-options {
display: grid;
grid-template-columns: 1fr 1fr;
justify-content: center;
text-align: center;
}

.close-assign {
    grid-column: span 2;
}


/*buttons */
.buttons-wrapper {
    display: grid;
    
    grid-template-columns: 1fr 1fr;
}


.btn {
    margin: .4rem;
    padding: .5rem 3rem;
    background-color: #262424;
    border: #000 1px solid;
    cursor: pointer;
    cursor: pointer;
    text-align: center;
}



</style>
